<script>
    export default {
        data() {
            return {};
        },
        methods: {
            download:function() {
                let apk, downlink
                try {
                    this.$http.get('/api/board/download-apk', {responseType:'blob'}).then(res => {
                        apk = new Blob([res.data], {type:res.headers['content-type']});
                        downlink = document.createElement('a');
                        downlink.href = window.URL.createObjectURL(apk);
                        downlink.download = res.headers['content-disposition'].split('=')[1];
                        downlink.click();
                    }).catch(err => {
                        throw new Error('data is not found!! [' + err.message + ']');
                    });
                } catch (e) {
                    console.log(e.message);
                } finally {
                    window.URL.revokeObjectURL(apk);
                }
            }
        }
    }
</script>
