<template>
    <div id="popup" class="confirm_z-index">
		<div class="dark_wall"></div>
        <div class="messagebox_loading_message">
            <div class="title">{{title}}</div>
            <div class="messagebox_loading_x" @click="close">X</div>
            <p :class="{m_btm: message != null && message.length > 0}">{{message}}</p>
            <div class="day_start_end_pop_btn" @click="confirmOk" v-if="boxType == 'confirm'">확인</div>
            <div class="day_start_end_pop_btn" @click="close">{{btnNm}}</div>            
        </div>
    </div>
</template>

<script>
    export default {
        name: 'msgBox',
        data() {
            return { }
        }, props: ['title', 'message', 'boxType', 'callback'],
        methods: {
            close: function() {
                this.$emit('close', false);
            }, confirmOk: function() {
                this.$emit('close', false);
                this.$emit('callback');
            }
        }, computed: {
            btnNm:function() {
                var nm;
                if (this.boxType == 'confirm') {
                    nm = '취소';
                } else {
                    nm = '확인';
                }
                return nm;
            }
        }
    }
</script>

<style lang="less">
    .messagebox_loading_message p {margin:0;};
    .messagebox_loading_message .m_btm{
        margin:0 0 20px 0;
    }
</style>