<template>
  <div id="app">
    <layout-hub :pMenuNum="pMenuNum" :propsObj="propsObj">
      <router-view :key="$route.fullPath" @setMenu="checkMenuNum" @setProps="setProps" :propsObj="propsObj" />
    </layout-hub>
  </div>
</template>

<script>
  import layoutHub from '@/layout/layoutHub';
  const filter = "win16|win32|win64|mac|macintel";
  export default {
    name: 'App',
    components: {
      layoutHub
    }, data() {
      return {
        pMenuNum: null,
        propsObj: null
      }
    },
    created() {
      if(navigator.platform) {
        if (filter.indexOf(navigator.platform.toLowerCase()) < 0) {
          this.$router.replace('/mobile');
        }
      }
    },methods: {
      checkMenuNum: function(val) {
        this.pMenuNum = val;
      },
      setProps: function(val) {
        this.propsObj = val;
      }
    }
  }
</script>

<style lang="less">
@import './assets/styles/style.css';
</style>
