<template>
    <div>
        <b>근무 승인</b>
        <div class="content_in_start"></div>
        <select v-model="year">
            <option v-for="(item, idx) in yearList" :value="item" v-bind:key="idx">{{item}} 년도</option>
        </select>
        <select v-model="month">
            <option value="">월</option>
            <option v-for="(item, idx) in monthList" :value="item" v-bind:key="idx">{{item}} 월</option>
        </select>
        <div class="search_btn" @click="search">검색</div>
        <br /><br />
        <table>
            <thead>
                <tr>
                    <th>이 름</th>
                    <th>근무 시작일</th>
                    <th>근무 종료일</th>
                    <th>근무 시간</th>
                    <th>요청구분</th>
                    <th>처리구분</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, idx) in confirmList" v-bind:key="idx">
                    <td>{{item.USER_NAME}}</td>
                    <td>{{item.WORKING_START_DATE}}</td>
                    <td>{{item.WORKING_END_DATE}}</td>
                    <td>{{item.WORKING_START_TIME}} ~ {{item.WORKING_END_TIME}}</td>
                    <td>{{item.CONFIRM_STATUS_CODE}}</td>
                    <td><div class="tdbtn" @click="callConfirm(item.IDX, CONFIRM)">승인</div><div class="tdbtn" style="margin-left:10px" @click="callConfirm(item.IDX, DENY)">반려</div></td>
                </tr>
            </tbody>
        </table>
        <paging-navi v-if="totSize !== null" :tot-size="chgTotal" 
            :div-size="pageSize" :curr-page="currPage" @mvPage="mPg => {currPage = mPg}" />

        <msgbox :title="boxTitle" :message="boxMsg" :boxType="boxType" v-if="showMsgBox != null && showMsgBox" @close="showMessagebox" @callback="procConfirm" />
    </div>
</template>

<script>
    import msgbox from '@/components/messageBox';
    import pagingNavi from '@/components/pagingNavi';
    const pageSize = 10;

    export default {
        name: 'workConfirmList',
        components: {
            msgbox, pagingNavi
        },
        data() {
            return {
                boxTitle: null,
                boxMsg: null,
                boxType: 'confirm',
                isShow: false,
                year: null,
                month: null,
                yearList: [],
                monthList: [],
                confirmList: [],
                idx: null,
                confirmType: null,
                pageSize: pageSize,
                currPage : 1,
                totSize: null,
                CONFIRM: 2,
                DENY: 3
            }
        }, created() {
            this.$emit('setMenu', 3); // 좌측메뉴 표기

            var today = new Date();
            this.year = today.getFullYear();

            // 기본 값 설정
            this.yearList.push(this.year);
            this.yearList.push(this.year + 1);
            for(var x = 1; x < 13; x++) {
				var temp = x < 10? '0' + x : x;
				this.monthList.push(temp);
			}

            var temp = (today.getMonth() + 1);
            this.month = temp < 10? '0' + temp : temp;

            this.init();

        }, computed: {
            showMsgBox: function() {
                return this.isShow;
            },
            chgTotal: function() {
                return this.totSize;
            }
        }, watch: {
            currPage: function(newval, oldval) {
                this.getList();
            }
        }, methods: {
            showMessagebox: function(val) {
                this.isShow = val;
            }, search: function() {
                this.currPage = 1;
                this.getTotal();
                this.getList();
            }, getList: function() {
                var url = "/api/work-schedule-confirm/" + this.currPage + '/' + pageSize;
                var query = "?year=" + this.year;
                if (this.month != null && this.month > 0) {
                    query += "&month=" + this.month;
                }
                url += query;

                this.$store.dispatch('getApiCall', url).then(data => {
                    var tempList = [];
                    data.forEach(elem => {
                        elem.WORKING_START_DATE = this.commonUtil.getDateFormat(elem.WORKING_START_DATE);
                        elem.WORKING_END_DATE = this.commonUtil.getDateFormat(elem.WORKING_END_DATE);
                        delete elem.REC_TIME;
                    });
                    this.confirmList = data;
                });
            }, getTotal: function() {
                var url = "/api/work-schedule-confirm/total-size";
                var query = "?year=" + this.year;
                if (this.month != null && this.month > 0) {
                    query += "&month=" + this.month;
                }
                url += query;

                this.$store.dispatch('getApiCall', url).then(data => {
                    this.totSize = data[0].TOTAL_SIZE;
                });
            }, callConfirm: function(idx, code) {
                if (code == this.CONFIRM) {
                    this.showMessagebox (true, 'confirm', '승인처리', '해당 요청을 승인하시겠습니까?');
                } else if (code == this.DENY) {
                    this.showMessagebox (true, 'confirm', '반려처리', '해당 요청을 반려하시겠습니까?');
                }
                this.idx = idx;
                this.confirmType = code;
            }, procConfirm: function() {
                var elemObj;
                var isOk = this.confirmList.some(elem => {
                    if (elem.IDX == this.idx) {
                        elemObj = elem;
                        return true;
                    }
                });
                if (!isOk) {
                    this.showMessagebox(true, 'alert', '근무 승인 실패', '해당 승인 정보를 찾을 수 없습니다.');
                    return false;
                }

                var sendObj = new Object();
                sendObj.userFrom = this.confirmType;
                sendObj.idx = this.idx;
                sendObj.userId = elemObj.USER_ID;
                sendObj.startDate = elemObj.WORKING_START_DATE;
                sendObj.startTime = elemObj.WORKING_START_TIME;
                sendObj.endDate = elemObj.WORKING_END_DATE;
                sendObj.endTime = elemObj.WORKING_END_TIME;
                sendObj.workType = elemObj.WORKING_TYPE;
                sendObj.url = '/api/proc/put-work-schedule';

                this.$store.dispatch('postApiCall', sendObj).then(res => {
					if (!res.isSuccess) {
						this.showMessagebox(true, 'alert', '근무 승인 실패', res.message);
						return;
					} else {
						this.showMessagebox(true, 'alert', '승인 성공', '근무 등록 요청이 완료되었습니다.');
						this.init();
						return;
					}
				});
            }, showMessagebox: function(val, mode, title, msg) {                
				this.boxType = mode;
				this.boxTitle = title;
				this.boxMsg = msg;

				this.isShow = val;
            },
            init: function() {
                this.getTotal();
                this.getList();
            }
        }
    }
</script>
<style lang="less">
    .content_in table td .tdbtn{
        width:50px;
    }
</style>