<template></template>
<script>
    // csv별 header 정의
    const jungSanHeader = [
        'NO', '단지', 'SITE', 'LINE', 'WaferSize', '사업부', 'MAIN 공정', '세부공정', '세부공정(PCS)', '사용호기',
        'Chamber', '업체', 'TYPE', '전작업일자', '교체일자', '사용기간', '품명', '보증기간', '교체수량', '단가',
        '환율', '부품비', '인건비', '금액', '비고', '신규단가', '기존단가', '차액', '구분', '최초수선여부',
        '개조여부', '건수', 'PJT구분', '세부PJT구분', '해당월', '시작시간', '종료시간', '할증유형', '할증율', '교체사유',
        'CostCenter', 'JobCode', '비고', '항목', '실측대상', '실측유무', '규격', '자재코드', '금액처리', 'SCR` MODEL',
        'S/N', '부품기준', 'S/N부여', '탈착S/N', '부착S/N', '반입S/N', '보증기간기준', '이전교체일', '유/무상 (이전교체일-교체일)', '무상->유상시사유',
        'EMS등록여부', 'WRS NO.', 'EMS미등록사유', 'EMS미등록사유(세부)', 'EMS등록일', 
        '반입일자(평택)', '배관번호(인화)(평택)', 'BAY(인화)(평택)', '비고(평택)', 'PUMP CHAMBER(평택)',
        'Type2(기흥)', '할증비(기흥)', '기준인원(기흥)', '기술팀(성명)(기흥)', '작업자(성명)(기흥)', '작업인원(기흥)', '세정유무(기흥)', '정산방법(기흥)', '작업 구분(기흥)', '기술팀(기흥)', '주차(기흥)', '신규 WARRANTY(기흥)'
    ];
    const checkHistHeader = [
        '설비호기', '점검일', '점검시간', '점검자', 'Pressure Temp확인', 'Manifold Chamber', 'Quench Drain H Wet 컬럼', 'Tank 열교환기', 'Drain 라인 (풀림 진동)', 
        '설비 내.외부 2S 상태', '전원인가 상태 (Power Vaccine) 타워램프 터치스크린', '통신 Cable 연결상태', '조치 필요 사항', '비고'
    ];

    // csv별 json key 정의
    const jungSanKey = [
        'WORK_NO','WORK_COMPLEX_NAME','WORK_SITE_NAME','WORK_LINE_NAME','EQP_WAFER_SIZE_CODE','WORK_PART_NAME','WORK_PROCESS_NAME','WORK_SUB_PROCESS_NAME','PCS_NAME','SCR_MODEL_NAME'
        ,'CHAMBER_NAME','SCR_MAKER_NAME','EQP_TYPE_NAME','WRS_PREV_WORKING_DATE','PART_CHANGE_DATE','PART_USED_TERM','PART_NAME','PART_WARRANTY','PART_CHANGE_COUNT','THIS_YEAR_UNIT_PRICE'
        ,'PART_CURRENT_MONEY','PART_UNIT_PRICE','PERSON_UNIT_PRICE','TOTAL_PRICE','PART_SECTION3','NEW_UNIT_PRICE','OLD_UNIT_PRICE','DIFFERENCE_PRICE','PART_PRICE_UPDOWN','IS_FIRST_FIXED_EQP'
        ,'IS_REMODEL_EQP','IS_REMODEL_COUNT','WORK_DEVISION_PJT','WORK_DEVISION_PJT_DETAIL','WRS_WORKING_MONTH','WRS_WORKING_START_TIME','WRS_WORKING_END_TIME','EXTRA_CHARGE_TYPE','EXTRA_CHARGE_RATE','CHANGE_REASON'
        ,'COST_CENTER','JOB_CODE','ETC','ITEM','IS_SURVEY_EQP','CHECK_SURVEY_EQP','PART_ITEM_SPEC_CODE','PART_CODE','PART_TREAT_MONEY','Expr1'
        ,'EQP_SN','PART_TYPE','IS_PART_GRANT_SN','IS_DESORPTION_PART_GRANT_SN','PART_PUT_SN','PART_ENTERING_PUT_SN','PART_WARRANTY_SPEC','PART_CHANGE_PREV_DATE','IS_PART_CHANGE_CHARGE','PART_CHANGE_CHARGE_REASON'
        ,'WORK_LINE_EMS_CODE','WRS_CODE','EMS_UNREGISTER_REASON','USED_EMS_UNREGISTER_REASON','EMS_REGISTER_DATE',
        'EQP_SCR_CARRY_DATE','EQP_MAIN_DUCT_NO','WORK_BAY_CODE', 'ETC_PYT', 'PUMP_CHAMBER_PYT',
        'TYPE2_GH', 'EXTRA_CHARGE_RATE_GH', 'NUMBER_OF_WORKER_GH', 'TECHNICAL_TEAM_NAME_GH', 'WORKER_NAME_GH', 'WORKER_COUNT_GH', 'IS_CLEAN_GH', 'CHARGE_METHOD_GH', 'WORK_TYPE_GH', 'TECHNICAL_TEAM_GH', 'PARKING_GH', 'NEW_WARRANTY_GH'
    ];
    const checkHistKey = ['EQP_SN', 'CHECK_DAY', 'CHECK_TIME', 'USER_NAME', 'CHK1', 'CHK2', 'CHK3', 'CHK4', 'CHK5', 'CHK6', 'CHK7', 'CHK8', 'COMMENT', 'ETC'];

    // csv별 url 정의
    const jungSanUrl = "/api/wrs-account-list";
    const checkHistUrl = "/api/eqp-check-hist";

    export default {
        data() {
            return {
                header: [],
                bodyList: [],
                jsonKeys: [],
                outputCsv: null,
                filename: null,
                csvType: null,
                url: null,
                condition: null
            };
        }, 
        props: ['propsObj'],
        created() {
            this.setDefaultData();
            this.makeCSV();
        },
        watch: {
            propsObj: function(newval) {
                this.propsObj = newval;
                this.setDefaultData();
                this.makeCSV();
            }
        }, methods: {
            setDefaultData: function() {
                this.csvType = this.propsObj.mode;
                this.condition = this.propsObj.condition;
            },
            makeCSV: async function() {
                try {
                    // 기초값 세팅 단계
                    var isGo = this.readyAction();
                    if (!isGo) {
                        throw new Error('data set err!!');
                    }
                    // 데이터 조회 단계
                    isGo = await new Promise((resolve, reject) => {
                        this.inqueryAction().then(() => {
                            resolve(true);
                        }, function() {
                            resolve(false);
                        });
                    });
                    if (!isGo) {
                        throw new Error('data is not found!!');
                    }
                    // 데이터 가공 단계
                    isGo = this.customizeAction();
                    if (!isGo) {
                        throw new Error('data customize fail..');
                    }
                    // csv 출력 데이터 생성 단계
                    isGo = this.makeAction();
                    if (!isGo) {
                        throw new Error('csv data make fail..');
                    }

                    this.downAction();
                } catch(e) {
                    this.$emit('fail', e.message);
                } finally {
                    this.$emit('finish', null);
                }
            },
            readyAction: function() {
                if (this.csvType == null || this.csvType.length < 1)  {
                    return false;
                }
                switch(this.csvType) {
                    case "JUNG_SAN":
                        this.header = jungSanHeader;
                        this.jsonKeys = jungSanKey;
                        this.url = jungSanUrl;
                        this.filename = "정산_" + new Date().toLocaleDateString().trim();
                        break;
                    case "CHECK_HIST":
                        this.header = checkHistHeader;
                        this.jsonKeys = checkHistKey;
                        this.url = checkHistUrl;
                        this.filename = "점검현황_" + this.condition.eqpSn;
                        break;
                    default:
                        console.log('not choice');
                }
                
                return true;
                
            },
            inqueryAction: async function() {
                if (this.url == null || this.url.length < 1) {
                    console.log('not found url!!');
                    return false;
                }
                var query = "";
                switch(this.csvType) {
                    case "JUNG_SAN":
                        if (this.condition != null) {
                            query += "?"; 
                            if (this.condition.workCode != null && this.condition.workCode.length > 0) {
                                query += "workCode=" + this.condition.workCode;
                            }
                            if (this.condition.sDate != null && this.condition.sDate.length > 0) {
                                query += "sDate=" + this.condition.sDate;
                            }
                            if (this.condition.eDate != null && this.condition.eDate.length > 0) {
                                // query between 조건 시 하루 +
                                var tempday = new Date(this.condition.eDate);
                                tempday.setDate(tempday.getDate() + 1);
                                var eDate = this.commonUtil.getDateStr(tempday);

                                query += "&eDate=" + eDate;
                            }
                            if (this.condition.placeCd != null && this.condition.placeCd.length > 0) {
                                query += "&placeCd=" + this.condition.placeCd;
                            }
                            if (this.condition.partCd != null && this.condition.partCd.length > 0) {
                                query += "&partCd=" + this.condition.partCd;
                            }
                            if (this.condition.complexCd != null && this.condition.complexCd.length > 0) {
                                query += "&complexCd=" + this.condition.complexCd;
                            }
                            if (this.condition.lineCd != null && this.condition.lineCd.length > 0) {
                                query += "&lineCd=" + this.condition.lineCd;
                            }
                        }
                        console.log(query);
                        break;
                    case "CHECK_HIST":
                        if (this.condition != null) {
                            query += "/" + this.condition.sDate + "/" + this.condition.eDate + "/" + this.condition.eqpSn;
                        }
                        break;
                    default:
                        console.log('not choice');
                }

                this.url += query;
                return await new Promise((resolve, reject) => {
                    this.$store.dispatch('getApiCall', this.url).then(data => {
                        this.bodyList = data;
                        if (data.length < 1) {
                            reject(false);
                        } else {
                            resolve(true);
                        }
                    }, function() {
                        reject(false);
                    });
                });
            },
            customizeAction: function() {
                switch(this.csvType) {
                    case "JUNG_SAN":
                        this.bodyList.forEach((elem, idx) => {
                            this.bodyList[idx].WRS_PREV_WORKING_DATE = this.commonUtil.dateTimeTransServer(elem.WRS_PREV_WORKING_DATE);
                            this.bodyList[idx].WRS_WORKING_START_TIME = this.commonUtil.dateTimeTransServer(elem.WRS_WORKING_START_TIME);
                            this.bodyList[idx].WRS_WORKING_END_TIME = this.commonUtil.dateTimeTransServer(elem.WRS_WORKING_END_TIME);
                            this.bodyList[idx].EQP_SCR_CARRY_DATE = this.commonUtil.dateTimeTransServer(elem.EQP_SCR_CARRY_DATE);
                        });
                        break;
                    case "CHECK_HIST":
                        this.bodyList.forEach((elem, idx) => {
                            this.bodyList[idx].CHECK_DAY = this.commonUtil.getDateFormat(elem.CHECK_DATE);
                            this.bodyList[idx].CHECK_TIME = this.commonUtil.getTimeFormat(elem.CHECK_DATE);
                        });
                        break;
                    default:
                        console.log('not choice');
                }
                return true;
            },
            makeAction: function() {
                var outputCsv = new Array();
                outputCsv.push(this.header.join(','));
                this.bodyList.forEach(elem => {
                    var row = new Array();
                    this.jsonKeys.forEach(key => {
                        row.push(elem[key]);
                    });
                    outputCsv.push(row.join(','));
                });

                this.outputCsv = outputCsv.join('\n');
                return true;
            },
            downAction: function() {
                let csvFile, downloadlink;
                try {
                    csvFile = new Blob(["\ufeff"+this.outputCsv], {type:"text/csv;charset=utf-8;"});
                    downloadlink = document.createElement("a");
                    downloadlink.download = this.filename + ".csv";
                    downloadlink.href=window.URL.createObjectURL(csvFile);
                    downloadlink.style.display = "none";
                    document.body.appendChild(downloadlink);
                    downloadlink.click();
                    document.body.removeChild(downloadlink);
                } catch (e) {
                    this.$emit('fail', e.message);
                } finally {
                    window.URL.revokeObjectURL(csvFile);
                }
            }
        }
    }

</script>