<template>
    <div>
        <b>출퇴근 현황</b>
        <div class="content_in_start"></div>
        <select v-model="year">
            <option v-for="(item, idx) in yearList" :value="item" v-bind:key="idx">{{item}} 년도</option>
        </select>
        <select v-model="month">
            <option value="">월</option>
            <option v-for="(item, idx) in monthList" :value="item" v-bind:key="idx">{{item}} 월</option>
        </select>
        <span v-if="false">
            <input type="text" placeholder="이름입력" style="height:38px" v-model="userNm">
        </span>
        <div class="search_btn" @click="search">검색</div>
        <br /><br />
        <div v-if="historyList.length > 0">
            <table>
                <thead>
                    <tr>
                        <th>이름</th>
                        <th>날짜</th>
                        <th>출근시간</th>
                        <th>출근등록</th>
                        <th>퇴근시간</th>
                        <th>퇴근등록</th>
                        <th>근무시간(시간)</th>
                        <th>지각(분)</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item, idx) in historyList" v-bind:key="idx">
                        <td>{{item.USER_NAME}}</td>
                        <td>{{item.CURRENT_YEAR}}-{{item.CURRENT_MONTH}}-{{item.CURRENT_DAY}}</td>
                        <td>{{item.GO_WORKING_TIME}}</td>
                        <td>{{item.GO_WORKING_CODE}}</td>
                        <td>{{item.LEAVE_WORKING_TIME}}</td>
                        <td>{{item.LEAVE_WORKING_CODE}}</td>
                        <td>{{item.TODAY_WORKING_TIME}}</td>
                        <td>{{item.LATE_TIME}}</td>
                    </tr>
                </tbody>
            </table>

            <paging-navi v-if="totSize !== null && totSize > pageSize" :tot-size="totSize" :div-size="pageSize" :curr-page="currPage" @mvPage="mPg => {currPage = mPg}" />
        </div>
        <div v-else>
            <span>조회된 데이터가 없습니다.</span>
        </div>
    </div>
</template>
<script>
    import pagingNavi from '@/components/pagingNavi';
    const pageSize = 10;

    export default{
        components: {
            pagingNavi
        },
        data(){
            return {
                pageSize: pageSize,
                currPage : 1,
                totSize: null,
                year: null,
                month: null,
                day: null,
                historyList: [],
                yearList: [],
                monthList: [],
                userNm: null,
                userId: this.$store.getters.getId
            };
        }, created() {
            this.$emit('setMenu', 22); // 좌측메뉴 표기
            var today = new Date();
            this.year = today.getFullYear();
            var temp = (today.getMonth() + 1);
            this.month = temp < 10? '0' + temp : temp;

            // 기본 값 설정
            this.yearList.push(this.year);
            this.yearList.push(this.year - 1);
            this.yearList.push(this.year - 2);
            for(var x = 1; x < 13; x++) {
				var temp = x < 10? '0' + x : x;
				this.monthList.push(temp);
			}
            this.search();
        }, watch: {
            currPage: function(newval, oldval) {
                this.getList();
            }
        }, methods: {
            search: function() {
                this.currPage = 1;
                this.getTotal();
                this.getList();
            },
            getList: function() {
                this.getTotal(); // 전체 값 조회

                var url = '/api/work-status-hist/' + this.currPage + '/' + pageSize;
                var query = '?year=' + this.year;
                if (this.month != null && this.month.length > 0) {
                    query += '&month=' + this.month;
                }
                if (this.day != null && this.day.length > 0) {
                    query += '&day=' + this.day;
                }
                if (this.userId != null && this.userId.length > 0) {
                    query += '&userId=' + this.userId;
                }
                if (this.userNm != null && this.userNm.length > 0) {
                    query += '&userNm=' + this.userNm;
                }
                url += query;
                this.$store.dispatch('getApiCall', url).then(data => {
                    var tempArr = [];
                    data.forEach((elem, idx) => {
                        elem.GO_WORKING_TIME = this.commonUtil.getTimeFormat(elem.GO_WORKING_TIME);
                        elem.LEAVE_WORKING_TIME = this.commonUtil.getTimeFormat(elem.LEAVE_WORKING_TIME);

                        tempArr.push(elem);
                    });
                    this.historyList = tempArr;
                });
            }, getTotal: function() {
                var url = '/api/work-status-hist/total-size';
                var query = '?year=' + this.year;
                if (this.month != null && this.month.length > 0) {
                    query += '&month=' + this.month;
                }
                if (this.day != null && this.day.length > 0) {
                    query += '&day=' + this.day;
                }
                if (this.userId != null && this.userId.length > 0) {
                    query += '&userId=' + this.userId;
                }
                if (this.userNm != null && this.userNm.length > 0) {
                    query += '&userNm=' + this.userNm;
                }
                url += query;

                this.$store.dispatch('getApiCall', url).then(data => {
                    this.totSize = data[0].TOTAL_SIZE;
                });
            }
        }
    }
</script>