<template>
    <div>
        <b>{{this.boardNm}}</b>
        <div class="content_in_start"></div>
        <br>

        <table class="board">
            <tr>
                <th>제 목</th>
                <td style="background-color:white"><input type="text" v-model="title" :readonly="!doWrite"></td>
            </tr>
            <tr>
                <th>공개범위</th>
                <td style="background-color:white">
                    <dataSelectBox v-model="openTeam" :items="teamCodeList" :input_id="'workTeamCode'" @input="val => {openTeam = val}" :viewText="'전체'" :setVal="openTeam" :disabled="!doWrite" style="width:400px"/> 
                </td>
            </tr>
            <tr>
                <th>내 용</th>
                <td style="background-color:white"><textarea v-model="contents" placeholder="작성하세요" :readonly="!doWrite"></textarea></td>
            </tr>
        </table>

        <br />
        <div class="btn" @click="putBoardMain" v-if="doWrite">저장</div>
        <div class="btn" @click="doWrite=true" v-if="!doWrite">수정하기</div>
        <div class="btn" @click="$router.go(-1)">목록으로</div>

        <component :is="ableFile == 'Y'? 'uploadForm': ''" :refIdx="idx"></component>
        <component :is="ableReply == 'Y'? 'replyForm': ''" :refIdx="idx"></component>
    </div>
</template>

<script>
    import dataSelectBox from '@/components/dataSelectBox';
    import replyForm from '@/components/replyForm';
    import uploadForm from '@/components/fileUpload';

    export default{
        components: {
            dataSelectBox, replyForm, uploadForm
        },
        data() {
            return {
                idx: null,
                boardNo: null,
                boardNm: null,
                openTeam: null,
                title: null,
                contents: null,
                fileYn: 'N',
                replyYn: 'N',
                regId: this.$store.getters.getId,
                regNm: this.$store.getters.getName,
                regTm: this.commonUtil.getToday(),
                teamCodeList: [],
                doWrite: true,
                ableFile: 'N',
                ableReply: 'N'
            }
        },
        created() {
            this.boardNo = this.$route.query.BOARD_NO;
            var menuNum;
            if (this.boardNo == 100) {
                menuNum = 70;
            } else if (this.boardNo == 200) {
                menuNum = 60;
            }
            this.$emit('setMenu', menuNum); // 좌측메뉴 표기
            this.boardNm = this.$route.query.BOARD_NAME;
            this.ableFile = this.$route.query.ABLE_FILE;
            this.ableReply = this.$route.query.ABLE_REPLY;
            this.idx = this.$route.query.idx;
            if (this.idx != null) {
                this.getDetail();
                this.doWrite = false;
            }

            this.commonUtil.getTeamCode().then(data => {
                this.teamCodeList = data;
            });
        }, methods: {
            getDetail() {
                var url = "/api/board/detail/" + this.idx;
                this.$store.dispatch('getApiCall', url, null).then(data => {
                    this.idx = data[0].IDX;
                    this.openTeam = (data[0].OPEN_TEAM == null)? null:data[0].OPEN_TEAM;
                    this.title = data[0].TITLE;
                    this.contents = data[0].CONTENTS;
                    this.fileYn = data[0].FILE_YN;
                    this.replyYn = data[0].REPLY_YN;
                    this.regId = data[0].REG_ID;
                    this.regNm = data[0].REG_NM;
                    this.regTm = data[0].MOD_TIME;
                });
            },
            putBoardMain() {
                var url = '/api/board/proc-board';
                this.$store.dispatch('postApiCall', {
                        url: url,
                        idx: this.idx,
                        boardNo: this.boardNo,
                        openTeam: this.openTeam,
                        title: this.title,
                        contents: this.contents,
                        fileYn: this.fileYn,
                        replyYn: this.replyYn,
                        regId: this.regId,
                        regNm: this.regNm
                }).then(data => {
                    alert("수정이 완료되었습니다.");
                    this.doWrite = false;
                });
            }
        }
    }
</script>