import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import TeamCodeView from '../views/manage/teamCode.vue'
import GroupCodeView from '../views/manage/groupCode.vue'
import articleList from '../views/board/articleList'
import readView from '../views/board/readView'
import loginView from '../views/loginView'
import UserConfirmListView from '../views/user/UserConfirmListView'
import userDetailView from '../views/user/userDetailView'
import workSchedule from '../views/work/workSchedule'
import workStatusHist from  '../views/work/workStatusHistory'
import workConfirmList from '@/views/work/workConfirmList'
import eqpCheckStatus from  '@/views/work/eqpCheckStatus'
import registEventCheck from  '@/views/work/registEventCheck'
import eventCheckHist from '@/views/work/eventCheckHist'
import eqpCheckHist from '@/views/work/eqpCheckHist'
import registWRSCode from '@/views/work/registWRSCode'
import wrsCodeHist from '@/views/work/wrsCodeHist'
import otConfirmList from '@/views/work/otConfirmList'
import mobile from '@/views/mobile'
import violationList from  '@/views/env/violationList'
import commonCode from '@/views/manage/commonCode'

Vue.use(VueRouter)

const routes = [
  {
    path: '/manage/teamCode',
    name: 'teamCode',
    component: TeamCodeView
  },
  {
    path: '/manage/groupCode',
    name: 'groupCode',
    component: GroupCodeView
  },
  {
    path: '/manage/commonCode',
    name: 'commonCode',
    component: commonCode
  },
  {
    path: '/board/articleList/:boardNo',
    name: 'articleList',
    component: articleList,
    props: true
  },
  {
    path: '/board/readView',
    name: 'readView',
    component: readView
  },
  {
    path: '/loginView',
    name: 'loginView',
    component: loginView,
    meta: {unauthorized:true, layout: 'noneLayout'}
  },
  {
    path: '/UserConfirmListView',
    name: 'UserConfirmListView',
    component: UserConfirmListView
  },
  {
    path: '/join/:mode/',
    name: 'joinView',
    component: userDetailView,
    meta: {unauthorized:true, layout: 'clearLayout'}
  },
  {
    path: '/mobile',
    name: 'mobile',
    component: mobile,
    meta: {unauthorized:true, layout: 'noneLayout'}
  },
  {
    path: '/userDetailView/:mode/:userId',
    name: 'userDetailView',
    component: userDetailView
  },
  {
    path: '/workScheduleView',
    name: 'workSchedule',
    component: workSchedule
  },
  {
    path: '/workStatusHist',
    name: 'workStatusHist',
    component: workStatusHist
  },
  {
    path: '/workConfirmList',
    name: 'workConfirmList',
    component: workConfirmList
  },
  {
    path: '/eqpCheckStatus',
    name: 'eqpCheckStatus',
    component: eqpCheckStatus
  },
  {
    path: '/registEventCheck',
    name: 'registEventCheck',
    component: registEventCheck
  },
  {
    path: '/eventCheckHist',
    name: 'eventCheckHist',
    component: eventCheckHist
  },
  {
    path: '/eqpCheckHist',
    name: 'eqpCheckHist',
    component: eqpCheckHist
  },
  {
    path: '/wrsCodeHist',
    name: 'wrsCodeHist',
    component: wrsCodeHist
  },
  {
    path: '/registWRSCode',
    name: 'registWRSCode',
    component: registWRSCode
  },
  {
    path: '/otConfirmList',
    name: 'otConfirmList',
    component: otConfirmList
  },
  {
    path: '/violationList',
    name: 'violationList',
    component: violationList
  },
  {
    path: '/',
    //name: 'Home',
    //component: Home,
    //meta: {unauthorized:true, layout: 'noneLayout'}
    redirect: '/loginView'
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
