<template>
    <div id="popup">
		<div class="dark_wall"></div>
		<div class="messagebox_loading_message_table">
			<div class="title">누락 설비 목록</div>
			<div class="messagebox_loading_x" @click="close">X</div>
            <div class="messagebox_loading_message_table_overflow">
                <table>	
                    <tr v-for="(item, idx) in loseList" v-bind:key="idx">
                        <td>{{idx + 1}}</td>
                        <td>{{item.EQP_SN}}</td>
                    </tr>    
                </table>  
            </div>
        </div>
	</div>
</template>

<script>
    export default {
        data() {
            return {
                loseList: []
            };
        },
        created() {
            this.getList();
        },
        props: ['propsObj'],
        methods: {
            getList: function() {
                var url = "/api/miss-check-list/" + this.propsObj.CHECK_DATE + "/" + this.propsObj.CHECK_TYPE;
                var query = "?lineCd=" + this.propsObj.WORK_LINE_CD;
                url += query;

                this.$store.dispatch('getApiCall', url).then(data => {
                    this.loseList = data;
                });
            },
            close: function() {
                this.$emit('close');
            }
        }
    }
</script>