<template>
    <div>
        <div class="dropbox">
            <input class="input-file" type="file" name="upFile" @change="addFile($event)">
            <ul>
                <li v-for="(item, idx) in fileList" v-bind:key='idx'>{{item.name}}<span style="float:right" @click="removeFile(idx)">삭제</span></li>
            </ul>
        </div>
        <button @click="storeFile()">파일전송</button>
    </div>
</template>
<script>
    export default{
        data() {
            return {
                fileList: new Array(),
                regId: this.$store.getters.getId,
                doAction: false
            };
        }, props: ['refIdx'],
        watch: {
            doAction: function(val) {
                if (val) {
                    this.storeFile();
                }
            }
        },
        methods: {
            addFile: function(event) {
                if (event.target.files[0] == null) {
                    return false;
                }
                if (this.fileList.length > 1) {
                    alert('파일 첨부는 1개만 가능합니다.');
                    return false;
                }
                this.fileList.push(event.target.files[0]);
            },
            removeFile: function(idx) {
                this.fileList.splice(idx, 1);
            },
            storeFile: function () {
                if (this.fileList.length < 1) {
                    alert('전송 할 파일 목록이 없습니다.');
                    return false;
                }
                const formData = new FormData();
                formData.append('url', '/api/board/upload');
                formData.append('refIdx', this.refIdx);
                formData.append('regId', this.regId);
                console.log(this.fileList);
                this.fileList.forEach(x => {
                    formData.append('upFile', x);
                });
                
                this.$store.dispatch('postApiCall', formData).then(res => {
                    if (res.isSuccess) {
                        alert(res.message);
                        this.fileList = [];
                    }
                })
            }
        }
    }
</script>

<style lang="less">

.dropbox {
    outline: 2px dashed #aaa;
    background: #7fb4dd;
    width: 300px;
    height: 100px;
    position: relative; 
     margin: 0 auto;
} 

.dropbox > ul > li{
    text-align:left;
    font-size:13px;
    line-height:20px;
    top: 50px;
    left: 0;
    z-index: 5;
}
.input-file{
    position: absolute;
    opacity: 0;
    width:calc(100% - 40px);
    height:100%;
    top:0;
    left:0;
     z-index: 3;
}
</style>