<template>
    <div>
        <b>공통코드 관리</b>
        <div class="content_in_start"></div>
        <dataSelectBox v-model="workTable" :items="chooseList" :input_id="'workTable'" @input="val => {workTable = val}" :viewText="'대상 선택'" :setVal="workTable" /> 
        <br />
        <table>
            <thead>
                <tr>
                    <th>순번</th>
                    <th>코드</th>
                    <th style="width:calc(40%)">코드명</th>
                    <th>처리구분</th>
                </tr>
            </thead>
        </table>
    </div>
</template>

<script>
    const pageSize = 10;

    import dataSelectBox from '@/components/dataSelectBox';
    export default{
        components: {
            dataSelectBox
        },
        data() {
            return {
                workTable: null,
                codeList: [],
                viewList: [],
                chooseList: [
                    {title: '사업장코드', val: 'WORK_PLACE_CODE_TBL'},
                    {title: '사업부코드', val: 'WORK_PART_CODE_TBL'},
                    {title: '단지코드', val: 'WORK_COMPLEX_CODE_TBL'},
                    {title: '사이트코드', val: 'WORK_SITE_CODE_TBL'},
                    {title: '라인코드', val: 'WORK_LINE_CODE_TBL'},
                    {title: '층코드', val: 'WORK_FLOOR_CODE_TBL'},
                    {title: 'BAY코드', val: 'WORK_BAY_CODE_TBL'},
                    {title: '직급코드', val: 'USER_POSITION_CODE_TBL'},
                    {title: '직책코드', val: 'USER_DUTY_CODE_TBL'},
                    {title: 'EMS코드', val: 'EQP_LINE_EMS_CODE_TBL'},
                    {title: '공정코드', val: 'EQP_PROCESS_CODE_TBL'},
                    {title: '세부공정코드', val: 'EQP_SUB_PROC_CODE_TBL'},
                    {title: 'SCRUBBER CODE', val: 'EQP_SCR_CODE_TBL'},
                    {title: 'SCRUBBER MODEL CODE', val: 'EQP_SCR_MODEL_CODE_TBL'},
                    {title: 'EQP TYPE CODE', val: 'EQP_TYPE1_CODE_TBL'},
                    {title: 'EQP TYPE2 CODE', val: 'EQP_TYPE2_CODE_TBL'},
                    {title: '처리가스코드', val: 'EQP_GAS_INFO_TBL'},
                    {title: '연결배기코드', val: 'EQP_EXHAUST_CODE_TBL'},
                    {title: '연결폐수코드', val: 'EQP_WASTE_INFO_TBL'},
                    {title: '주설비 회사 코드', val: 'EQP_MAIN_MAKER_CODE_TBL'},
                    {title: '주설비 모델 코드', val: 'EQP_MAIN_MODEL_CODE_TBL'},
                    {title: 'SCRUBBER 회사 코드', val: 'EQP_SCR_MAKER_CODE_TBL'},
                    {title: 'SCRUBBER 관리 회사 코드', val: 'EQP_MGR_MAKER_CODE_TBL'},
                    {title: 'CHAMBER 코드', val: 'EQP_CHAMBER_CODE_TBL'},
                    {title: '위반사항 코드', val: 'ENV_VIOLATION_CODE_TBL'},
                    {title: '사고 코드', val: 'ENV_ACCIDENT_CODE_TBL'},
                    {title: '적발유형 코드', val: 'ENV_EXPOSE_CODE_TBL'},
                    {title: '사고유형 코드', val: 'ENV_ACCIDENT_TYPE_CODE_TBL'},
                    {title: '불합리 코드', val: 'ENV_IRRATION_CODE_TBL'},
                    {title: '불합리 사고 연관코드', val: 'ENV_IRRATION_WITH_ACCIDENT_CODE_TBL'},
                    {title: '알람코드', val: 'EQP_ALARM_CODE_TBL'}
                ],
                pageSize: pageSize,
                currPage : 1,
                totSize: null
            };
        },
        methods: {
            openArea: function() {

            },
            getList: function() {
                var url = this.getUrl();
                url += "?mode=list";

                this.$store.dispatch('getApiCall', url).then(data => {
                    this.codeList = data;

                    this.mappingField();
                });

            },
            getTotal: function() {
                var url = this.getUrl();
                url += "?mode=total";

                this.$store.dispatch('getApiCall', url).then(data => {
                    this.totSize = data[0].TOTAL_SIZE;
                });
            },
            getUrl: function() {
                var url = '';
                switch(this.workTable) {
                    case 'WORK_PLACE_CODE_TBL' :
                        url = '/place-code'
                        break;
                    case 'WORK_PART_CODE_TBL' :
                        url = '/part-code'
                        break;
                    case 'WORK_COMPLEX_CODE_TBL' :
                        url = '/complex-code'
                        break;
                    case 'WORK_LINE_CODE_TBL' :
                        url = '/line-code'
                        break;
                    case 'WORK_SITE_CODE_TBL' :
                        url = '/site-code'
                        break;
                    case 'WORK_FLOOR_CODE_TBL' :
                        url = '/floor-code'
                        break;
                    case 'WORK_BAY_CODE_TBL' :
                        url = '/bay-code'
                        break;
                    case 'USER_POSITION_CODE_TBL' :
                        url = '/position-code'
                        break;
                    case 'USER_DUTY_CODE_TBL' :
                        url = '/duty-code'
                        break;
                    case 'EQP_LINE_EMS_CODE_TBL' :
                        url = '/ems-code'
                        break;
                    case 'EQP_PROCESS_CODE_TBL' :
                        url = '/process-code'
                        break;
                    case 'EQP_SUB_PROC_CODE_TBL' :
                        url = '/sub-process-code'
                        break;
                    case 'EQP_SCR_CODE_TBL' :
                        url = '/scr-code'
                        break;
                    case 'EQP_SCR_MODEL_CODE_TBL' :
                        url = '/scr-model-code'
                        break;
                    case 'EQP_TYPE1_CODE_TBL' :
                        url = '/eqp-type1-code'
                        break;
                    case 'EQP_TYPE2_CODE_TBL' :
                        url = '/eqp-type2-code'
                        break;
                    case 'EQP_GAS_INFO_TBL' :
                        url = '/gas-code'
                        break;
                    case 'EQP_EXHAUST_CODE_TBL' :
                        url = '/exhaust-code'
                        break;
                    case 'EQP_WASTE_INFO_TBL' :
                        url = '/waste-code'
                        break;
                    case 'EQP_MAIN_MAKER_CODE_TBL' :
                        url = '/main-maker-code'
                        break;
                    case 'EQP_MAIN_MODEL_CODE_TBL' :
                        url = '/main-model-code'
                        break;
                    case 'EQP_SCR_MAKER_CODE_TBL' :
                        url = '/scr-maker-code'
                        break;
                    case 'EQP_MGR_MAKER_CODE_TBL' :
                        url = '/mgr-maker-code'
                        break;
                    case 'EQP_CHAMBER_CODE_TBL' :
                        url = '/chamber-code'
                        break;
                    case 'ENV_VIOLATION_CODE_TBL' :
                        url = '/violation-code'
                        break;
                    case 'ENV_ACCIDENT_CODE_TBL' :
                        url = '/accident-code'
                        break;
                    case 'ENV_EXPOSE_CODE_TBL' :
                        url = '/expose-code'
                        break;
                    case 'ENV_ACCIDENT_TYPE_CODE_TBL' :
                        url = '/accident-type-code'
                        break;
                    case 'ENV_IRRATION_CODE_TBL' :
                        url = '/irration-code'
                        break;
                    case 'ENV_IRRATION_WITH_ACCIDENT_CODE_TBL' :
                        url = '/irration-accident-code'
                        break;
                    case 'EQP_ALARM_CODE_TBL' :
                        url = '/alram-code'
                        break;
                    default:
                        url = '';
                        break;
                }

                if (url.length > 0) {
                    url = '/api/common' + url;
                }

                return url;
            },
            mappingField: function() {
                if (this.codeList.length < 1) {
                    console.log('[mappingField]not code data!!');
                    return false;
                }

                var keySet = Object.keys(this.codeList[0]);
                var tempList = [];
                this.codeList.forEach(elem => {
                    var tempObj = new Object();
                    keySet.forEach(key => {
                        if (key.indexOf('_CODE') > -1) {
                            tempObj.ITEM_CODE = elem[key];
                        } else if (key.indexOf('_NAME') > -1) {
                            tempObj.ITEM_NAME = elem[key];
                        }
                    });

                    tempList.push(tempObj);
                });

                this.viewList = tempList;   
            }
        }
    }
</script>