<template>
    <div>
        <b>위반사항</b>
        <div class="content_in_start"></div>
        <select v-model="year">
            <option v-for="(item, idx) in yearList" :value="item" v-bind:key="idx">{{item}} 년도</option>
        </select>
        <select v-model="month">
            <option value="">월</option>
            <option v-for="(item, idx) in monthList" :value="item" v-bind:key="idx">{{item}} 월</option>
        </select>
        <div class="search_btn" @click="search">검색</div>
        <br /><br />
        <div v-if="violationList.length > 0">
            <table>
                <thead>
                    <tr>
                        <th>설비호기</th>
                        <th>위반항목</th>
                        <th>위반내역</th>
                        <th>위반일시</th>
                        <th>위반자</th>
                        <th>감독관</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item, idx) in violationList" v-bind:key="idx">
                        <td>{{item.EQP_SN}}</td>
                        <td>{{item.VIOLATION_NAME}}</td>
                        <td>{{item.COMMENT}}</td>
                        <td>{{item.VIOLATION_DATE}}</td>
                        <td>{{item.USER_NAME}}</td>
                        <td>{{item.WRITER_NAME}}</td>
                    </tr>
                </tbody>
            </table>
            <paging-navi v-if="totSize !== null && totSize > pageSize" :tot-size="chgTotal" :div-size="pageSize" :curr-page="currPage" @mvPage="mPg => {currPage = mPg}" />
        </div>
        <div v-else>
            <span>조회된 데이터가 없습니다.</span>
        </div>
    </div>
</template>

<script>
    import pagingNavi from '@/components/pagingNavi';
    const pageSize = 10;

    export default {
        name: 'violationList',
        components: {
            pagingNavi
        },
        data() {
            return {
                year: null,
                month: null,
                yearList: [],
                monthList: [],
                violationList: [],
                pageSize: pageSize,
                currPage : 1,
                totSize: null
            }
        }, created() {
            this.$emit('setMenu', 31); // 좌측메뉴 표기

            var today = new Date();
            this.year = today.getFullYear();

            // 기본 값 설정
            this.yearList.push(this.year);
            this.yearList.push(this.year + 1);
            for(var x = 1; x < 13; x++) {
				var temp = x < 10? '0' + x : x;
				this.monthList.push(temp);
			}

            var temp = (today.getMonth() + 1);
            this.month = temp < 10? '0' + temp : temp;
            this.search();

        }, computed: {
            chgTotal: function() {
                return this.totSize;
            }
        }, watch: {
            currPage: function(newval, oldval) {
                this.getList();
            }
        }, methods: {
            search: function() {
                this.currPage = 1;
                this.getTotal();
                this.getList();
            }, getList: function() {
                var url = "/api/violation-list/" + this.year + '/' + this.month;
                var query = "?pageNo=" + this.currPage + "&pageSize=" + this.pageSize;
                url += query;

                this.$store.dispatch('getApiCall', url).then(data => {
                    var tempList = [];
                    data.forEach(elem => {
                        elem.VIOLATION_DATE = this.commonUtil.dateTimeTransServer(elem.VIOLATION_DATE);
                        tempList.push(elem);
                    });
                    this.violationList = tempList;
                });
            }, getTotal: function() {
                var url = "/api/violation-list/total-size/" + this.year + '/' + this.month;

                this.$store.dispatch('getApiCall', url).then(data => {
                    this.totSize = data[0].TOTAL_SIZE;
                });
            }
        }
    }
</script>