<template>
    <div>
        <b>팀코드 관리</b>
        <div class="content_in_start"></div>
        <div class="work_btn" @click="openArea(0)">팀코드 등록</div>
        <table>
            <thead>
                <tr>
                    <th style="width:calc(12%)">사업장</th>
                    <th style="width:calc(12%)">사업부</th>
                    <th style="width:calc(12%)">단지</th>
                    <th style="width:calc(12%)">라인</th>
                    <th style="width:calc(12%)">팀코드</th>
                    <th>팀이름</th>
                    <th style="width:calc(12%)">장비구분</th>
                    <th style="width:calc(12%)">처리요청</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, idx) in teamCodeList" v-bind:key="idx">
                    <td>{{item.WORK_PLACE_CODE}}</td>
                    <td>{{item.WORK_PART_CODE}}</td>
                    <td>{{item.WORK_COMPLEX_CODE}}</td>
                    <td>{{item.WORK_LINE_CODE}}</td>
                    <td>{{item.WORK_TEAM_CODE}}</td>
                    <td>{{item.WORK_TEAM_NAME}}</td>
                    <td>{{item.TEAM_EQP_TYPE}}</td>
                    <td>
                        <div class="tdbtn" style="width:40px;" @click="openArea(1, idx)">수정</div>
                        <div class="tdbtn" style="width:40px;margin-left:7px" @click="delCheck(idx)">삭제</div>
                    </td>
                </tr>
            </tbody>
        </table>
        <div v-if="openInput">
            <br />
            <table>
                <tr>
                    <td style="width:calc(12%)">
                        <dataSelectBox v-model="objData.placeCd" :items="list.placeCode" :input_id="'placeCode'" @input="val => {objData.placeCd = val}" :viewText="'사업장 선택'" /> 
                    </td>
                    <td style="width:calc(12%)">
                        <dataSelectBox v-model="objData.partCd" :items="list.partCode" :input_id="'partCode'" @input="val => {objData.partCd = val}" :viewText="'사업부 선택'" 
                            :setVal="objData.partCd" :disabled="list.partCode.length < 1" /> 
                    </td>
                    <td style="width:calc(12%)">
                        <dataSelectBox v-model="objData.complexCd" :items="list.complexCode" :input_id="'complexCode'" @input="val => {objData.complexCd = val}" :viewText="'단지 선택'" 
                            :disabled="list.complexCode.length < 1" :setVal="objData.complexCd" />
                    </td>
                    <td style="width:calc(12%)">
                        <dataSelectBox v-model="objData.lineCd" :items="list.lineCode" :input_id="'lineCode'" @input="val => {objData.lineCd = val}" :viewText="'라인 선택'" 
                            :disabled="list.lineCode.length < 1" :setVal="objData.lineCd" /> 
                    </td>
                    <td style="width:calc(12%)"><input type="text" v-model="objData.teamCd" placeholder="팀코드" :readonly="this.objData.userFrom != 0"></td>
                    <td><input type="text" v-model="objData.teamNm" placeholder="팀이름"></td>
                    <td style="width:calc(12%)">
                        <dataSelectBox v-model="objData.eqpType" :items="list.eqpType" :input_id="'lineCode'" @input="val => {objData.eqpType = val}" :viewText="'장비 선택'" 
                            :disabled="list.eqpType.length < 1" :setVal="objData.eqpType" /> 
                    </td>
                    <td style="width:calc(12%)">
                        <div class="tdbtn" style="width:40px;" @click="checkValid">저장</div>
                        <div class="tdbtn" style="width:40px;margin-left:7px" @click="closeArea">닫기</div>
                    </td>
                </tr>
            </table>
        </div>
        <msgbox :title="boxObj.title" :message="boxObj.msg" :boxType="boxObj.bType" v-if="showMsgBox != null && showMsgBox" @close="showMessagebox" @callback="procRegister" />
    </div>
</template>
<script>
    import dataSelectBox from '@/components/dataSelectBox';
    import msgbox from  '@/components/messageBox';
    export default {
        components: {
            dataSelectBox, msgbox
        },
        data() {
            return {
                list: {
                    placeCode: [],
                    partCode: [],
                    complexCode: [],
                    lineCode: [],
                    eqpType: [
                        {val:0, title:'스크러버'},
                        {val:1, title:'칠러'}
                    ]
                },
                objData: {
                    userFrom: 0,
                    placeCd: null,
                    partCd: null,
                    complexCd: null,
                    lineCd: null,
                    teamCd: null,
                    teamNm: null,
                    eqpType: null,
                    userId: this.$store.getters.getId
                },
                boxObj: {
                    title: null,
                    msg: null,
                    bType: null
                },
                teamCodeList: [],
                openInput: false,
                isShow: false
            };
        },
        created() {
            this.$emit('setMenu', 81); // 좌측메뉴 표기
            this.getList();
        },
        computed: {
            showMsgBox: function() {
                return this.isShow;
            }
        },
        methods: {
            getList: function() {
                var url = "/api/common/team-code";
                this.$store.dispatch('getApiCall', url).then(data => {
                    this.teamCodeList = data;
                });
            },
            openArea: async function(mode, idx) {
                this.closeArea();
                
                this.objData.userFrom = mode;
                try {
                    this.$store.commit('SET_LOADING', true); //로딩 화면 호출

                    if (this.list.placeCode.length < 1) {
                        await this.commonUtil.getPlaceList().then(data => {
                            this.list.placeCode = data;
                        });
                    }

                    if (this.list.partCode.length < 1) {
                        await this.commonUtil.getPartList().then(data => {
                            this.list.partCode = data;
                        });
                    }

                    if (this.list.complexCode.length < 1) {
                        await this.commonUtil.getComplexList().then(data => {
                            this.list.complexCode = data;
                        });
                    }
                    if (this.list.lineCode.length < 1) {
                        await this.commonUtil.getLineList().then(data => {
                            this.list.lineCode = data;
                        });
                    }

                    if (idx != null) {
                        this.objData.placeCd = this.teamCodeList[idx].WORK_PLACE_CODE;
                        this.objData.partCd = this.teamCodeList[idx].WORK_PART_CODE;
                        this.objData.complexCd = this.teamCodeList[idx].WORK_COMPLEX_CODE;
                        this.objData.lineCd = this.teamCodeList[idx].WORK_LINE_CODE;
                        this.objData.teamCd = this.teamCodeList[idx].WORK_TEAM_CODE;
                        this.objData.teamNm = this.teamCodeList[idx].WORK_TEAM_NAME;
                        this.objData.eqpType = this.teamCodeList[idx].TEAM_EQP_TYPE;
                    }
                    this.openInput = true;
                } catch(err) {
                    console.log(err.message);
                } finally {
                    this.$store.commit('SET_LOADING', false); //로딩 화면 닫음
                }
            },
            closeArea: function() {
                this.objData = {
                    userFrom: 0,
                    placeCd: null,
                    partCd: null,
                    complexCd: null,
                    lineCd: null,
                    teamCd: null,
                    teamNm: null,
                    eqpType: null,
                    userId: this.$store.getters.getId
                }

                this.openInput = false;
            },
            checkValid: function() {
                if (this.objData.teamCd == null || this.objData.teamCd.length < 1) {
                    this.showMessagebox(true, 'alert', '데이터 확인', '팀 코드가 입력되지않았습니다.');
                    return false;
                }
                if (this.objData.teamNm == null || this.objData.teamNm.length < 1) {
                    this.showMessagebox(true, 'alert', '데이터 확인', '팀 이름이 입력되지않았습니다.');
                    return false;
                }

                if (this.objData.userFrom == 0) {
                    this.showMessagebox(true, 'confirm', '등록', '팀코드를 등록하시겠습니까?');
                } else if (this.objData.userFrom == 1) {
                    this.showMessagebox(true, 'confirm', '수정', '팀코드 정보를 수정하시겠습니까?');
                } else {
                    return false;
                }
            },
            delCheck: function(idx) {
                this.objData.userFrom = 2;
                this.objData.placeCd = this.teamCodeList[idx].WORK_PLACE_CODE;
                this.objData.partCd = this.teamCodeList[idx].WORK_PART_CODE;
                this.objData.complexCd = this.teamCodeList[idx].WORK_COMPLEX_CODE;
                this.objData.lineCd = this.teamCodeList[idx].WORK_LINE_CODE;
                this.objData.teamCd = this.teamCodeList[idx].WORK_TEAM_CODE;
                this.objData.teamNm = this.teamCodeList[idx].WORK_TEAM_NAME;
                this.objData.eqpType = this.teamCodeList[idx].TEAM_EQP_TYPE;

                this.showMessagebox(true, 'confirm', '삭제', this.objData.teamCd + '팀코드를 삭제하시겠습니까?');
            },
            procRegister: function() {
                this.objData.url = "/api/proc/put-team-code";
                this.$store.dispatch('postApiCall', this.objData).then(data => {
                    if (!data.isSuccess) {
						this.showMessagebox(true, 'alert', '요청 실패', data.message);
						return;
					} else {
                        if (this.objData.userFrom == 0) {
                            this.showMessagebox(true, 'alert', '팀코드 등록', '팀코드 등록이 완료되었습니다.');
                        } else if (this.objData.userFrom == 1) {
                            this.showMessagebox(true, 'alert', '팀코드 수정', '팀코드 수정이 완료되었습니다.');
                        }
                        this.closeArea();
                        this.getList();
						return;
					}
                })
            },
            showMessagebox: function(val, mode, title, msg) {                
				this.boxObj.bType = mode;
				this.boxObj.title = title;
				this.boxObj.msg = msg;
				this.isShow = val;
            }
        }
    }
</script>

<style type="less">
    .content_in table td select {
        width:calc(100% - 10px);padding-left:15px
    }
    .content_in table td select:disabled {
        width:calc(100% - 10px);padding-left:15px
    }
</style>