<template>
    <div>
        <b>이벤트 점검</b>
        <div class="content_in_start"></div>
        <br />
        <table class="form">
            <tr>
                <td style="width:100px;">이벤트제목</td>
                <td><input type="text" v-model="dataObj.title" ></td>
            </tr>
            <tr>
                <td>점검일</td>
                <td >
                    <input type="date" placeholder="날짜선택" v-model="dataObj.checkDay" style="width:200px;height:30px">
                </td>
            </tr>
            <tr>
                <td>사업장</td>
                <td>
                    <dataSelectBox v-model="placeCd" :items="placeList" :input_id="'place'" @input="val => {placeCd = val}" :viewText="'사업장 선택'" /> 
                </td>
            </tr>
            <tr>
                <td>사업부</td>
                <td>
                    <dataSelectBox v-model="partCd" :items="partList" :input_id="'part'" @input="val => {partCd = val}"  :viewText="'사업부 선택'" :disabled="partList.length < 1" :setVal="partCd" /> 
                </td>
            </tr>
            <tr>
                <td>단지</td>
                <td>
                    <dataSelectBox v-model="complexCd" :items="complexList" :input_id="'complex'" @input="val => {complexCd = val}" :viewText="'단지 선택'" :disabled="complexList.length < 1" :setVal="complexCd" /> 
                </td>
            </tr>
            <tr>
                <td>라인</td>
                <td><dataSelectBox v-model="dataObj.lineCd" :items="lineList" :input_id="'line'" @input="val => {dataObj.lineCd = val}" :viewText="'라인 선택'" :disabled="lineList.length < 1" :setVal="dataObj.lineCd" /> </td>
            </tr>
        </table>
        <br>
        <div class="btn" @click="checkRegister">점검 등록</div>
        <div class="btn" @click="goHist">이력 보기</div>

        <msgbox :title="boxObj.title" :message="boxObj.msg" :boxType="boxObj.bType" v-if="showMsgBox != null && showMsgBox" @close="showMessagebox" @callback="procRegister" />
    </div>
</template>

<script>
    import dataSelectBox from '@/components/dataSelectBox';
    import msgbox from  '@/components/messageBox';
    export default{
        components: {
            dataSelectBox, msgbox
        },
        data() {
            return {
                placeCd: null,
                partCd: null,
                complexCd: null,
                dataObj: {
                    title: null,
                    checkDay: null,
                    placeCd: null,
                    partCd: null,
                    complexCd: null,
                    lineCd: null,
                    placeAll: 1,
                    partAll: 1,
                    complexAll: 1,
                    status: 0,
                    userFrom: 0,
                    regId: this.$store.getters.getId
                },
                boxObj: {
                    title: null,
                    msg: null,
                    bType: null
                },
                placeList:[],
                partList: [],
                complexList: [],
                lineList: [],
                isShow: false
            };
        }, created() {
            this.$emit('setMenu', 13); // 좌측메뉴 표기
            var today = new Date();
            var month = (today.getMonth() + 1) < 10? '0' + (today.getMonth() + 1):(today.getMonth() + 1);
            var day = today.getDate() < 10? '0' + today.getDate():today.getDate();
            this.dataObj.checkDay = today.getFullYear() + '-' + month + '-' + day;
            this.getPlaceList();
        }, watch: {
            placeCd: function(newval) {
                this.clearValue('place');

                this.dataObj.placeCd = newval;
                if (newval == null || newval.length < 1) {
                    this.dataObj.placeAll = 1
                    return false;
                } 

                this.dataObj.placeAll = 0;
                this.getPartList();
            },
            partCd: function(newval) {
                this.clearValue('part');

                this.dataObj.partCd = newval;
                if (newval == null || newval.length < 1) {
                    this.dataObj.partAll = 1
                    return false;
                } 

                this.dataObj.partAll = 0;
                this.getComplexList();
            },
            complexCd: function(newval) {
                this.clearValue('complex');

                this.dataObj.complexCd = newval;
                if (newval == null || newval.length < 1) {
                    this.dataObj.complexAll = 1
                    return false;
                } 

                this.dataObj.complexAll = 0;
                this.getLineList();
            }
        }, computed: {
            showMsgBox: function() {
                return this.isShow;
            }
        }, methods: {
            checkRegister: function() {
                if (this.dataObj.checkDay == null) {
                    this.showMessagebox(true, 'alert', '점검 등록', '점검일이 선택되지 않았습니다.');    
                    return false;
                }
                this.showMessagebox(true, 'confirm', '점검 등록', '이벤트 점검을 등록하시겠습니까?');
            },
            procRegister: function() {
                this.dataObj.url = '/api/proc/put-event-check';
                this.$store.dispatch('postApiCall', this.dataObj).then(data => {
                    if (!data.isSuccess) {
						this.showMessagebox(true, 'alert', '점검 등록 실패', data.message);
						return;
					} else {
						this.showMessagebox(true, 'alert', '점검 등록', '이벤트 점검 등록이 완료되었습니다.');
                        this.clearValue('all');
						return;
					}
                });
            },
            getPlaceList: function() {
                this.commonUtil.getPlaceList().then(data => {
                    this.placeList = data;
                });
            },
            getPartList: function() {
                this.commonUtil.getPartList().then(data => {
                    this.partList = data;
                });
            },
            getComplexList: function() {
                this.commonUtil.getComplexList().then(data => {
                    this.complexList = data;
                });
            },
            getLineList: function() {
                this.commonUtil.getLineList().then(data => {
                    this.lineList = data;
                });
            },
            clearValue: function(step) {
                this.lineList = [];
                this.dataObj.lineCd = '';
                if (step == 'complex') {
                    return false;
                }

                this.complexList = [];
                this.dataObj.complexCd = '';
                this.complexCd = '';
                this.dataObj.complexAll = 1;
                if (step == 'part') {
                    return false;
                }

                this.partList = [];
                this.dataObj.partCd = '';
                this.partCd = '';
                this.dataObj.partAll = 1;
                if (step == 'place') {
                    return false;
                }

                this.dataObj.title = null;
                this.dataObj.placeCd = '';
                this.placeCd = '';
                this.dataObj.placeAll = 1;
            },
            showMessagebox: function(val, mode, title, msg) {                
				this.boxObj.bType = mode;
				this.boxObj.title = title;
				this.boxObj.msg = msg;
				this.isShow = val;
            },
            goHist: function() {
                this.$router.push('/eventCheckHist');
            }
        }
    }
</script>