<template>
    <div>
        <b>이벤트 점검 이력</b>
        <div class="content_in_start"></div>
        <select v-model="year">
            <option v-for="(item, idx) in yearList" :value="item" v-bind:key="idx">{{item}} 년도</option>
        </select>
        <select v-model="month">
            <option value="">월</option>
            <option v-for="(item, idx) in monthList" :value="item" v-bind:key="idx">{{item}} 월</option>
        </select>
        <div class="search_btn" @click="getList">검색</div>
        <br /><br />
        <div v-if="histList.length > 0">
            <table>
                <thead>
                    <tr>
                        <th style="min-width:150px">이벤트 제목</th>
                        <th style="width:90px">점검일</th>
                        <th>사업장</th>
                        <th>점검상태</th>
                        <th style="width:90px">투입인원</th>
                        <th style="width:90px">투입시간</th>
                        <th>비고</th>
                        <th>완료</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item, idx) in histList" v-bind:key="idx">
                        <td>{{item.EVENT_TITLE}}</td>
                        <td>{{item.EVENT_CHECK_DAY}}</td>
                        <td>{{item.WORK_PLACE_NAME}} / {{item.WORK_PART_NAME}} / {{item.WORK_COMPLEX_NAME}} / {{item.WORK_LINE_NAME}}</td>
                        <td>{{item.CHECK_STATUS}}</td>
                        <td>{{item.EVENT_NUMBER_OF_PERSON}}</td>
                        <td>{{item.EVENT_TOTAL_CHECK_TIME}} <span style="font-size:13px;" v-if="item.EVENT_TOTAL_CHECK_TIME != null"> 분</span></td>
                        <td>
                            <div v-if="item.isInput">
                                <input type="text" style="width:calc(100% - 90px);" v-model="item.EVENT_NOTE">
                                <div class="tdbtn" style="width:40px" @click="checkRegData(idx)">등록</div>
                            </div>
                            <div v-if="!item.isInput">
                                <div style="display:inline-block;width:calc(100% - 59px)">{{item.EVENT_NOTE}}</div>
                                <div class="tdbtn" style="width:40px;" @click="doInput(idx)">수정</div>
                            </div>
                        </td>
                        <td>
                            <div class="tdbtn" style="width:70px;" @click="checkComplete(idx)" v-if="item.CHECK_STATUS != '점검완료'">점검완료</div>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        <div v-else>
            <span>조회된 데이터가 없습니다.</span>
        </div>
        <msgbox :title="boxObj.title" :message="boxObj.msg" :boxType="boxObj.bType" v-if="showMsgBox != null && showMsgBox" @close="showMessagebox" @callback="callbackHub" />
    </div>
</template>

<script>
    import msgbox from '@/components/messageBox';

    export default{
        components: {
            msgbox
        },
        data() {
            return {
                yearList: [],
                monthList: [],
                histList: [],
                year: null,
                month: null,
                isShow: false,
                boxObj: {
                    title: null,
                    msg: null,
                    bType: null
                }, dataObj: {
                    procType: null, // 0: 비고등록, 1: 이벤트점검 완료
                    idx: null,
                    note: null,
                    url: null,
                    eventCode: null,
                    eventStatus: null
                }
            };
        }, created() {
            this.$emit('setMenu', 14); // 좌측메뉴 표기
            var today = new Date();
            this.year = today.getFullYear();
            var temp = (today.getMonth() + 1);
            this.month = temp < 10? '0' + temp : temp;

            // 기본 값 설정
            this.yearList.push(this.year);
            this.yearList.push(this.year - 1);
            this.yearList.push(this.year - 2);
            for(var x = 1; x < 13; x++) {
				var temp = x < 10? '0' + x : x;
				this.monthList.push(temp);
			}

            this.getList();
        }, computed: {
            showMsgBox: function() {
                return this.isShow;
            }
        }, methods: {
            checkRegData: function(idx) {
                //console.log(this.histList[idx].EVENT_NOTE);
                if(this.histList[idx].EVENT_NOTE == null ||this.histList[idx].EVENT_NOTE.length < 1) {
                    this.showMessagebox(true, 'alert', '알 림', '등록 내용을 입력해주세요.');    
                    return false;
                }

                this.dataObj.procType = 0;
                this.dataObj.idx = this.histList[idx].IDX;
                this.dataObj.note = this.histList[idx].EVENT_NOTE;
                this.showMessagebox(true, 'confirm', '등 록', '해당 내용을 등록하시겠습니까?');
            },
            procRegister: function() {
                this.dataObj.url = "/api/proc/put-eventcheck-info";
                this.$store.dispatch('postApiCall', this.dataObj).then(res => {
                    if (!res.isSuccess) {
						this.showMessagebox(true, 'alert', '등록 실패', res.message);
						return;
					} else {
						this.showMessagebox(true, 'alert', '성 공', '이벤트 점검 내용 등록이 완료되었습니다.');
                        this.getList();
						return;
					}
                });
            },
            getList: function() {
                var url = "/api/event-check-hist/" + this.year;
                if (this.month != null && this.month.length > 0) {
                    url += "?month=" + this.month;
                }

                this.$store.dispatch('getApiCall', url).then (data => {
                    var tempList = [];
                    data.forEach(elem => {
                        elem.EVENT_CHECK_DAY = this.commonUtil.getDateFormat(elem.EVENT_CHECK_DAY);
                        elem.WORK_PLACE_NAME = elem.WORK_PLACE_NAME == null? '전체':elem.WORK_PLACE_NAME; 
                        elem.WORK_PART_NAME = elem.WORK_PART_NAME == null? '-':elem.WORK_PART_NAME;
                        elem.WORK_COMPLEX_NAME = elem.WORK_COMPLEX_NAME == null? '-':elem.WORK_COMPLEX_NAME;
                        elem.WORK_LINE_NAME = elem.WORK_LINE_NAME == null? '-':elem.WORK_LINE_NAME;
                        elem.isInput = elem.EVENT_NOTE == null ? true:false;
                        tempList.push(elem);
                    });
                    this.histList = tempList;
                });
            },
            doInput: function(idx) {
                this.histList[idx].isInput = true;
            },
            checkComplete: function(idx) {
                this.dataObj.procType = 1;
                this.dataObj.eventCode = this.histList[idx].EVENT_CODE;
                this.dataObj.eventStatus = 2;
                this.showMessagebox(true, 'confirm', '점검완료', '점검 완료처리를 하시겠습니까?');
            },
            doComplete: function(idx) {
                this.dataObj.url = "/api/proc/event-check-complete";
                this.$store.dispatch('postApiCall', this.dataObj).then(res => {
                   if (!res.isSuccess) {
						this.showMessagebox(true, 'alert', '완료 실패', res.message);
						return;
					} else {
						this.showMessagebox(true, 'alert', '완료', '이벤트 점검 처리가 완료되었습니다.');
                        this.getList();
						return;
					} 
                });
            },
            callbackHub: function() {
                if (this.dataObj.procType == 1) {
                    this.doComplete(); //이벤트 점검 처리
                } else {
                    this.procRegister(); //비고 등록 처리
                }
            },
            showMessagebox: function(val, mode, title, msg) {                
				this.boxObj.bType = mode;
				this.boxObj.title = title;
				this.boxObj.msg = msg;
				this.isShow = val;
            }
        }
    }
</script>