<template>
    <component :is="layout" :pMenuNum='pMenuNum' :propsObj='propsObj'>
        <slot />
    </component>
</template>

<script>
import noneLayout from './noneLayout';
import mainLayout from './mainLayout';
import clearLayout from './clearLayout';

export default {
    name: 'layoutHub',
    components: {
        noneLayout, mainLayout, clearLayout
    },
    data() {
        return {
        }
    },
    props:['pMenuNum', 'propsObj'],
    computed: {
        layout: function() {
            this.$store.dispatch('resetTime');
            return this.$route.meta.layout == null? 'mainLayout' : this.$route.meta.layout;
        }
    }
}
</script>