<template>
    <div class="login_wrap">
		<div class="login_pannel">
			<div class="login_cell">
				<img src="../assets/img/login_logo.png" alt="유니셈 스마트커스토머시스템"><br>
                <form @submit.prevent="doLogin(userId, userPw)">
                    <input type="text" placeholder="ID" v-model="userId"><br>
                    <input type="password" placeholder="PW" v-model="userPw"><br>
                    <button class="login_btn">로그인</button><br>
                    <div class="login_btn_under">
                        <input type="checkbox" id="save"> <label for="save">ID 저장</label>
                        <a href="#">ID, PW찾기</a>
                        <a href="javascript:;" @click="goJoin">회원가입</a>
                    </div>
                </form>
			</div>
			<div class="login_bg"></div>
		</div>
	</div>    
</template>

<script>
    export default {
        data() {
            return {
                userId: null,
                userPw: null,
                msg: '사용자 로그인 화면입니다.'
            }
        }, created() {
            this.doLogout();
        }, methods: {
            doLogin(userId, userPw) {
             this.$store.dispatch('doLogin', {userId, userPw}).then((data)=>{
                 if (!data.isLogin) {
                    alert('로그인에 실패하였습니다.');
                    //alert(data.message);
                    return false;
                 }
                 this.$router.replace({ path: '/board/articleList/100'});
             });
            }, doLogout() {
                this.$store.commit('LOGOUT');
            }, goJoin() {
                this.$router.push({name: 'joinView', params:{mode:0}});
            }
        }
    }

</script>