<template>
    <div>
        <b>작업 이력</b>
        <div class="content_in_start"></div>
        <input type="date" placeholder="날짜선택" v-model="startDate" > ~
        <input type="date" placeholder="날짜선택" v-model="endDate" > <br />
        <dataSelectBox v-model="placeCd" :items="placeList" :input_id="'placeCode'" @input="val => {placeCd = val}" :viewText="'사업장 선택'" :setVal="placeCd" /> 
        <dataSelectBox v-model="partCd" :items="partList" :input_id="'partCode'" @input="val => {partCd = val}" :viewText="'사업부 선택'" 
            :disabled="partList.length < 1" :setVal="partCd" /> 
        <dataSelectBox v-model="complexCd" :items="complexList" :input_id="'complexCode'" @input="val => {complexCd = val}" :viewText="'단지 선택'" 
            :disabled="complexList.length < 1" :setVal="complexCd" /> 
        <dataSelectBox v-model="lineCd" :items="lineList" :input_id="'lineCode'" @input="val => {lineCd = val}" :viewText="'라인 선택'" 
            :disabled="lineList.length < 1" :setVal="lineCd" /> 
        <div class="search_btn" @click="search">검색</div>
        <br />
        <div v-if="workList.length > 0">
            <div class="work_btn" @click="downFileCsv(null)">CSV 다운로드</div>
            <table>
                <thead>
                    <tr>
                        <th>설비호기</th>
                        <th>작업코드</th>
                        <th>WRS코드</th>
                        <th>의뢰제목</th>
                        <th style="width:170px">의뢰일시</th>
                        <th>작업자</th>
                        <th>작업상태</th>
                        <th>정산</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item, idx) in workList" v-bind:key="idx">
                        <td @click="goDetail(item)">{{item.WRS_EQP_ID}} / {{item.WRS_EQP_SN == null? '-':item.WRS_EQP_SN}}</td>
                        <td @click="goDetail(item)">{{item.WORK_CODE}}</td>
                        <td @click="goDetail(item)">{{item.WRS_CODE}}</td>
                        <td @click="goDetail(item)">{{item.WRS_REQ_TITLE}}</td>
                        <td @click="goDetail(item)">{{item.WRS_REC_TIME}}</td>
                        <td>{{item.WORK1}}</td>
                        <td>{{item.WORK_STATUS}}</td>
                        <td><img src="@/assets/img/jungsan.png" alt="" style="width:25px;vertical-align:middle"  @click="downFileCsv(item.WORK_CODE)"></td>
                    </tr>
                </tbody>
            </table>
            <paging-navi v-if="totSize !== null && totSize > pageSize" :tot-size="chgTotal" :div-size="pageSize" :curr-page="currPage" @mvPage="mPg => {currPage = mPg}" />
        </div>
        <div v-else>
            <br />
            <span>조회된 데이터가 없습니다.</span>
        </div>
        <outCsv :propsObj="doCSV" v-if="csvObj != null" @finish="val => {csvObj = val}" @fail="csvFail" />
        <msgbox :title="boxObj.title" :message="boxObj.msg" :boxType="boxObj.bType" v-if="showMsgBox != null && showMsgBox" @close="showMessagebox" />
    </div>
</template>
<script>
    import dataSelectBox from '@/components/dataSelectBox';
    import pagingNavi from '@/components/pagingNavi';
    import outCsv from '@/components/outputCSV';
    import msgbox from  '@/components/messageBox';
    const pageSize = 10;
    export default{
        components: {
            dataSelectBox, pagingNavi, outCsv, msgbox
        },
        data() {
            return {
                pageSize: pageSize,
                currPage : 1,
                totSize: null,
                startDate: null,
                endDate: null,
                placeCd: null,
                partCd: null,
                complexCd: null,
                lineCd: null,
                placeList: [],
                partList: [],
                complexList: [],
                lineList: [],
                workList: [],
                boxObj: {
                    title: null,
                    msg: null,
                    bType: null
                },
                isShow: false,
                csvObj: null
            };
        },
        props: ['propsObj'],
        async created() {
            this.$emit('setMenu', 16); // 좌측메뉴 표기
            await this.commonUtil.getPlaceList().then(data => {
                this.placeList = data;
            });
            this.endDate = this.commonUtil.getToday();

            // 오늘 부터 7일전 설정
            var beforeday = new Date();
            beforeday.setDate(beforeday.getDate() - 7);
            this.startDate = this.commonUtil.getDateStr(beforeday);

            if (this.propsObj != null) { //이전 검색 조건 세팅
                this.setCondition();
            }
        },
        computed: {
            chgTotal: function() {
                return this.totSize;
            },
            showMsgBox: function() {
                return this.isShow;
            },
            doCSV: function() {
                return this.csvObj;
            }
        },
        watch: {
            placeCd: function(newval) {
                this.clearValue('place');
                this.placeCd = newval;
                if (newval == null || newval.length < 1) {
                    return false;
                } 
                this.commonUtil.getPartList().then(data => {
                    this.partList = data;
                    if (this.propsObj != null) {
                        this.partCd = this.propsObj.partCd;
                        if (this.partCd.length < 1) {
                            this.search();
                        }
                    }
                });
            },
            partCd: function(newval, oldval) {
                this.clearValue('part');
                this.partCd = newval;
                if (newval == null || newval.length < 1) {
                    return false;
                } 
                this.commonUtil.getComplexList().then(data => {
                    this.complexList = data;
                    if (this.propsObj != null) {
                        this.complexCd = this.propsObj.complexCd;
                        if (this.complexCd.length < 1) {
                            this.search();
                        }
                    }
                });
            },
            complexCd: function(newval) {
                this.clearValue('complex');
                this.complexCd = newval;
                if (newval == null || newval.length < 1) {
                    return false;
                }
                this.commonUtil.getLineList().then(data => {
                    this.lineList = data;
                    if (this.propsObj != null) {
                        this.lineCd = this.propsObj.lineCd;
                        if (this.lineCd.length < 1) {
                            this.search();
                        }
                    }
                });
            }
        }, methods: {
            search: function() {
                this.$emit('setProps', null);
                if (this.placeCd == null || this.placeCd.length < 1) {
                    this.showMessagebox(true, 'alert', '이력 조회', '사업장 정보가 선택되지 않았습니다.');
                    return false;
                }
                this.getTotal();
                this.getList();
            },
            getList: function() {
                // query between 조건 시 하루 +
                var tempday = new Date(this.endDate);
                tempday.setDate(tempday.getDate() + 1);
                var eDate = this.commonUtil.getDateStr(tempday);

                var url = "/api/wrs-work-hist/" + this.startDate + "/" + eDate;
                var query = "?pageNo=" + this.currPage + "&pageSize=" + pageSize;
                if (this.placeCd != null && this.placeCd.length > 0) {
                    query += "&placeCd=" + this.placeCd;
                }
                if (this.partCd != null && this.partCd.length > 0) {
                    query += "&partCd=" + this.partCd;
                }
                if (this.complexCd != null && this.complexCd.length > 0) {
                    query += "&complexCd=" + this.complexCd;
                }
                if (this.lineCd != null && this.lineCd.length > 0) {
                    query += "&lineCd=" + this.lineCd;
                }
                url += query;
                this.$store.dispatch('getApiCall', url).then(data => {
                    var tempList = [];
                    data.forEach(elem => {
                        elem.WRS_REC_TIME = this.commonUtil.dateTimeTransServer(elem.WRS_REC_TIME);
                        elem.WRS_EXPECT_DAY = this.commonUtil.dateTimeTransServer(elem.WRS_EXPECT_DAY);
                        tempList.push(elem);
                    });
                    this.workList = tempList;
                });
            },
            getTotal: function() {
                // query between 조건 시 하루 +
                var tempday = new Date(this.endDate);
                tempday.setDate(tempday.getDate() + 1);
                var eDate = this.commonUtil.getDateStr(tempday);

                var url = "/api/wrs-work-hist/total-size/" + this.startDate + "/" + eDate;
                var query = "?";
                if (this.placeCd != null && this.placeCd.length > 0) {
                    query += "placeCd=" + this.placeCd;
                }
                if (this.partCd != null && this.partCd.length > 0) {
                    query += "&partCd=" + this.partCd;
                }
                if (this.complexCd != null && this.complexCd.length > 0) {
                    query += "&complexCd=" + this.complexCd;
                }
                if (this.lineCd != null && this.lineCd.length > 0) {
                    query += "&lineCd=" + this.lineCd;
                }
                url += query;
                this.$store.dispatch('getApiCall', url).then(data => {
                   this.totSize = data[0].TOTAL_SIZE;
                });
            },
            clearValue: function(step) {
                this.lineCd = '';
                this.lineList = [];
                if (step == 'complex') {
                    return false;
                }

                this.complexCd = '';
                this.complexList = [];
                if (step == 'part') {
                    return false;
                }

                this.partCd = '';
                this.partList = [];
                if (step == 'place') {
                    return false;
                }

                this.placeCd = '';
            },
            goDetail:function(obj) {
                obj.searchCond = {
                    sDate: this.startDate,
                    eDate: this.endDate,
                    currPage: this.currPage,
                    placeCd: this.placeCd,
                    partCd: this.partCd,
                    complexCd: this.complexCd,
                    lineCd: this.lineCd
                }

                this.$emit('setProps', obj);
                this.$router.push('/registWRSCode');
            }, showMessagebox: function(val, mode, title, msg) {                
				this.boxObj.bType = mode;
				this.boxObj.title = title;
				this.boxObj.msg = msg;
				this.isShow = val;
            }, downFileCsv: function(workCode) {
                var tempObj = new Object();
                tempObj.mode = 'JUNG_SAN';
                tempObj.condition = new Object();
                if (workCode != null) {
                    tempObj.condition.workCode = workCode;
                } else {
                    tempObj.condition.sDate = this.startDate;
                    tempObj.condition.eDate = this.endDate;
                    tempObj.condition.placeCd = this.placeCd;
                    tempObj.condition.partCd = this.partCd;
                    tempObj.condition.complexCd = this.complexCd;
                    tempObj.condition.lineCd = this.lineCd;
                }

                this.csvObj = tempObj;
            },
            csvFail: function(msg) {
                this.showMessagebox(true, 'alert', '파일 생성 실패', msg);
            }, setCondition: function() {
                this.startDate = this.propsObj.sDate;
                this.endDate = this.propsObj.eDate;
                this.currPage = this.propsObj.currPage;
                this.placeCd = this.propsObj.placeCd;
            }
        }
    }
</script>