<template>
    <div>
        <b>OT 승인</b>
        <div class="content_in_start"></div>
        <select v-model="year">
            <option v-for="(item, idx) in yearList" :value="item" v-bind:key="idx">{{item}} 년도</option>
        </select>
        <select v-model="month">
            <option value="">월</option>
            <option v-for="(item, idx) in monthList" :value="item" v-bind:key="idx">{{item}} 월</option>
        </select>
        <div class="search_btn" @click="search">검색</div>
        <br /><br />
        <div v-if="confirmList.length > 0">
            <table>
                <thead>
                    <tr>
                        <th style="width:130px">이 름</th>
                        <th style="width:130px">요청일</th>
                        <th style="width:130px">요청시간</th>
                        <th>요청사유</th>
                        <th style="width:130px">처리상태</th>
                        <th style="width:150px">처리</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item, idx) in confirmList" v-bind:key="idx">
                        <td>{{item.USER_NAME}}</td>
                        <td>{{item.REQ_DATE}}</td>
                        <td>{{item.OT_TIME}} 시간</td>
                        <td>{{item.WORK_COMMENT}}</td>
                        <td>{{item.REQ_CONFIRM}}</td>
                        <td><div class="tdbtn" @click="callConfirm(item.IDX, CONFIRM)">승인</div>
                            <div class="tdbtn" style="margin-left:10px" @click="callConfirm(item.IDX, DENY)">반려</div>
                        </td>
                    </tr>
                </tbody>
            </table>
            <paging-navi v-if="totSize !== null && totSize > pageSize" :tot-size="chgTotal" :div-size="pageSize" :curr-page="currPage" @mvPage="mPg => {currPage = mPg}" />
        </div>
        <div v-else>
            <span>조회된 데이터가 없습니다.</span>
        </div>
        <msgbox :title="boxTitle" :message="boxMsg" :boxType="boxType" v-if="showMsgBox != null && showMsgBox" @close="showMessagebox" @callback="procConfirm" />
    </div>
</template>

<script>
    import msgbox from '@/components/messageBox';
    import pagingNavi from '@/components/pagingNavi';
    const pageSize = 10;

    export default {
        name: 'otConfirmList',
        components: {
            msgbox, pagingNavi
        },
        data() {
            return {
                boxTitle: null,
                boxMsg: null,
                boxType: 'confirm',
                isShow: false,
                year: null,
                month: null,
                yearList: [],
                monthList: [],
                confirmList: [],
                idx: null,
                confirmType: null,
                pageSize: pageSize,
                currPage : 1,
                totSize: null,
                CONFIRM: 2,
                DENY: 3
            }
        }, created() {
            this.$emit('setMenu', 4); // 좌측메뉴 표기

            var today = new Date();
            this.year = today.getFullYear();

            // 기본 값 설정
            this.yearList.push(this.year);
            this.yearList.push(this.year + 1);
            for(var x = 1; x < 13; x++) {
				var temp = x < 10? '0' + x : x;
				this.monthList.push(temp);
			}

            var temp = (today.getMonth() + 1);
            this.month = temp < 10? '0' + temp : temp;

            this.init();

        }, computed: {
            showMsgBox: function() {
                return this.isShow;
            },
            chgTotal: function() {
                return this.totSize;
            }
        }, watch: {
            currPage: function(newval, oldval) {
                this.getList();
            }
        }, methods: {
            showMessagebox: function(val) {
                this.isShow = val;
            }, search: function() {
                this.currPage = 1;
                this.getTotal();
                this.getList();
            }, getList: function() {
                var url = "/api/ot-confirm-list/" + this.year + '/' + this.month;
                var query = "?pageNo=" + this.currPage + "&pageSize=" + this.pageSize;
                url += query;

                this.$store.dispatch('getApiCall', url).then(data => {
                    var tempList = [];
                    data.forEach(elem => {
                        elem.REQ_DATE = this.commonUtil.getDateFormat(elem.REQ_DATE);
                        tempList.push(elem);
                    });
                    this.confirmList = tempList;
                });
            }, getTotal: function() {
                var url = "/api/ot-confirm-list/total-size/" + this.year + '/' + this.month;

                this.$store.dispatch('getApiCall', url).then(data => {
                    this.totSize = data[0].TOTAL_SIZE;
                });
            }, callConfirm: function(idx, code) {
                if (code == this.CONFIRM) {
                    this.showMessagebox (true, 'confirm', '승인처리', '해당 요청을 승인하시겠습니까?');
                } else if (code == this.DENY) {
                    this.showMessagebox (true, 'confirm', '반려처리', '해당 요청을 반려하시겠습니까?');
                }
                this.idx = idx;
                this.confirmType = code;
            }, procConfirm: function() {
                var elemObj;
                var isOk = this.confirmList.some(elem => {
                    if (elem.IDX == this.idx) {
                        elemObj = elem;
                        return true;
                    }
                });
                if (!isOk) {
                    this.showMessagebox(true, 'alert', 'OT 승인 실패', '해당 승인 정보를 찾을 수 없습니다.');
                    return false;
                }

                var sendObj = new Object();
                sendObj.userFrom = this.confirmType;
                sendObj.idx = this.idx;
                sendObj.userId = elemObj.USER_ID;
                sendObj.reqDate = elemObj.REQ_DATE;
                sendObj.otTime = elemObj.OT_TIME;
                sendObj.workComment = elemObj.WORK_COMMENT;
                sendObj.url = '/api/proc/ot-confirm';

                this.$store.dispatch('postApiCall', sendObj).then(res => {
					if (!res.isSuccess) {
						this.showMessagebox(true, 'alert', '승인 실패', res.message);
						return;
					} else {
						this.showMessagebox(true, 'alert', '승인 성공', 'OT 신청이 완료되었습니다.');
						this.init();
						return;
					}
				});
            }, showMessagebox: function(val, mode, title, msg) {                
				this.boxType = mode;
				this.boxTitle = title;
				this.boxMsg = msg;

				this.isShow = val;
            },
            init: function() {
                this.getTotal();
                this.getList();
            }
        }
    }
</script>
<style lang="less">
    .content_in table td .tdbtn{
        width:50px;
    }
</style>