<template>
    <div class="paging" v-model="totSize">
        <a href="javascript:;" @click="mvPage(1)"> &lt;&lt; </a>
        <a href="javascript:;" @click="mvPage(currPage - 1)"> &lt; </a>
        <span style="font-weight:bold;" v-if="pageNumList[0] > 1"> .... </span>
        <a href="javascript:;" v-for="item in pageNumList" @click="mvPage(item)" :class="{active : item == currPage}">{{item}}</a>
        <span style="font-weight:bold;" v-if="pageNumList[pageNumList.length-1] < showPageNum"> .... </span>
        <a href="javascript:;" @click="mvPage(currPage + 1)"> &gt; </a>
        <a href="javascript:;" @click="mvPage(showPageNum)"> &gt;&gt; </a>
    </div>
</template>

<script>
    export default{
        name: "pagingNavi",
        data() {
            return {
                showPageNum: null,
                pageNumList: []
            };
        }, props: ['totSize', 'divSize', 'currPage'],
        created() {
            this.getShowPage();
        }, methods: {
            mvPage: function(pgNum) {
                if (pgNum < 1) {
                    pgNum = 1;
                }
                if (pgNum > this.showPageNum ) {
                    pgNum = this.showPageNum;
                }
                this.$emit('mvPage', pgNum);
            },
            getShowPage: function() {
                this.showPageNum = Math.ceil(this.totSize / this.divSize) == 0? 1:Math.ceil(this.totSize / this.divSize);
                var numList = [];
                var pageNum = this.showPageNum;
                if (pageNum > 10) {
                    var sPos = this.currPage - 5;
                    var ePos = this.currPage + 4;
                    if (sPos < 1) {
                        ePos -= sPos - 1;
                        sPos = 1;
                    }
                    if (ePos > pageNum) {
                        sPos -= (ePos - pageNum);
                        ePos = pageNum;
                    }
                    for(sPos; sPos <= ePos; sPos++) {
                        numList.push(sPos);
                    }
                } else {
                    for (var i=1; i <= this.showPageNum; i++) {
                        numList.push(i);
                    }
                }
                this.pageNumList = numList;
            }
        }, watch: {
            totSize: function(newval) {
                this.totSize = newval;
                this.getShowPage();
            },
            currPage: function(newval) {
                this.currPage = newval;
                this.getShowPage();
            }
        }
    }
</script>