<template>
    <div>
        <b>{{objInfo.BOARD_NAME}}</b>
        <div class="content_in_start"></div>
        <br>
        <div class="work_btn" @click="goWrite">글쓰기</div>
        <!-- button @click="download(4)">다운테스트</button -->
        <table>
            <thead>
                <tr>
                    <th>번호</th>
                    <th>제목</th>
                    <th>작성일</th>
                    <th>작성자</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, idx) in listArr" v-bind:key="idx">
                    <td>{{item.idx}}</td>
                    <td><router-link :to="{name: 'readView', query: {idx:item.idx, BOARD_NAME:objInfo.BOARD_NAME}}">{{item.title}}</router-link></td>
                    <td>{{item.reg_time}}</td>
                    <td>{{item.reg_nm}}</td>
                </tr>
            </tbody>
        </table>
        <br />
        <paging-navi v-if="totSize !== null" :tot-size="totSize" 
            :div-size="pageSize" :curr-page="currPage" @mvPage="mPg => {currPage = mPg}" />
    </div>
</template>

<script>
    import pagingNavi from '../../components/pagingNavi';

    const pageSize = 10;
    export default {
        components: {
            pagingNavi
        },
        data() {
            return {
                totSize: null,
                currPage : 1,
                pageSize : pageSize,
                listArr: [],
                objInfo: new Object()
            }
        },
        props:['boardNo'],
        created() {
            var menuNum;
            if (this.boardNo == 100) {
                menuNum = 70;
            } else if (this.boardNo == 200) {
                menuNum = 60;
            }
            this.$emit('setMenu', menuNum); // 좌측메뉴 표기
            this.getBoardInfo();
            this.getTotalSize();
            this.getList();
        }, watch: {
            currPage: function(newval, oldval) {
                this.getList();
            }
        }, methods: {
            getList() {
                var url = '/api/board/' + this.boardNo + '/' + this.currPage + '/' + pageSize;
                this.$store.dispatch('getApiCall', url, null).then(data => {
                    var tempList = [];
                    data.forEach(elem => {
                        elem.reg_time = this.commonUtil.getDateFormat(elem.reg_time);
                        tempList.push(elem);
                    });
                    this.listArr = tempList;
                });
            },
            getTotalSize() {
                var url = '/api/board/' + this.boardNo + '/total-size';

                this.$store.dispatch('getApiCall', url, null).then(data => {
                    this.totSize = data[0].TOTAL_SIZE;
                });
            },
            download:function(idx) {
                let objFile, downlink
                try {
                    this.$http.get('/api/board/downfile/' + idx, {responseType:'blob'}).then(res => {
                        objFile = new Blob([res.data], {type:res.headers['content-type']});
                        downlink = document.createElement('a');
                        downlink.href = window.URL.createObjectURL(objFile);
                        downlink.download = decodeURI(res.headers['content-disposition'].split('=')[1]);
                        downlink.click();
                    }).catch(err => {
                        throw new Error('data is not found!! [' + err.message + ']');
                    });
                } catch (e) {
                    console.log(e.message);
                } finally {
                    window.URL.revokeObjectURL(objFile);
                }
            },
            goWrite: function() {
                this.$router.push({name: 'readView', query: this.objInfo});
            },
            getBoardInfo:function() {
                var url = '/api/board/board-info/' + this.boardNo;
                this.$store.dispatch('getApiCall', url).then(data => {
                    this.objInfo = data[0];
                });
            }
        }
    }
</script>