<template>
    <div>
        <b>근무표 작성</b>
        <div class="content_in_start"></div>
        <br>
        <b><a href="javascript:;" @click="month = month-1">&lt;</a>{{year}}.{{month < 10? '0' + month:month}}<a href="javascript:;" @click="month = month+1">&gt;</a></b>
        <table class="cal_table" style="margin-top:10px">
            <thead>
                <tr>
					<th style="width:10%">주차</th>
					<th>일</th>
					<th>월</th>
					<th>화</th>
					<th>수</th>
					<th>목</th>
					<th>금</th>
					<th>토</th>
				</tr>
            </thead>
            <tbody>
                <tr v-for="(item, idx) in calendar" v-bind:key="idx">
                    <td>{{item[0]}}주차</td>
					<td class="today" @click="openSchedulePop(item[1])" :class="classObjList[idx][1]" :data-after="classObjList[idx][1] == null? '':classObjList[idx][1].printJoo" 
                        :data-before="classObjList[idx][1] == null? '':classObjList[idx][1].printYa">{{item[1]}}</td>
					<td class="today" @click="openSchedulePop(item[2])" :class="classObjList[idx][2]" :data-after="classObjList[idx][2] == null? '':classObjList[idx][2].printJoo" 
                        :data-before="classObjList[idx][2] == null? '':classObjList[idx][2].printYa">{{item[2]}}</td>
					<td class="today" @click="openSchedulePop(item[3])" :class="classObjList[idx][3]" :data-after="classObjList[idx][3] == null? '':classObjList[idx][3].printJoo" 
                        :data-before="classObjList[idx][3] == null? '':classObjList[idx][3].printYa">{{item[3]}}</td>
					<td class="today" @click="openSchedulePop(item[4])" :class="classObjList[idx][4]" :data-after="classObjList[idx][4] == null? '':classObjList[idx][4].printJoo" 
                        :data-before="classObjList[idx][4] == null? '':classObjList[idx][4].printYa">{{item[4]}}</td>
					<td class="today" @click="openSchedulePop(item[5])" :class="classObjList[idx][5]" :data-after="classObjList[idx][5] == null? '':classObjList[idx][5].printJoo" 
                        :data-before="classObjList[idx][5] == null? '':classObjList[idx][5].printYa">{{item[5]}}</td>
					<td class="today" @click="openSchedulePop(item[6])" :class="classObjList[idx][6]" :data-after="classObjList[idx][6] == null? '':classObjList[idx][6].printJoo" 
                        :data-before="classObjList[idx][6] == null? '':classObjList[idx][6].printYa">{{item[6]}}</td>
					<td class="today" @click="openSchedulePop(item[7])" :class="classObjList[idx][7]" :data-after="classObjList[idx][7] == null? '':classObjList[idx][7].printJoo" 
                        :data-before="classObjList[idx][7] == null? '':classObjList[idx][7].printYa">{{item[7]}}</td>
                </tr>
            </tbody>
        </table>

        <putWorkSchedule :propsObj="propsObj" @close="val => {propsObj = val}"v-if="propsObj" @reload="init" />
    </div>
</template>
<script>
import putWorkSchedule from '@/components/putWorkSchedule';
import $ from  '@/assets/script/jquery.min.js';
export default {
    name: 'workSchedule',
    components: {
        putWorkSchedule
    },
    data() {
        return {
            year: null,
            month: null,
            calendar : [],
            scheduleList: [],
            classObjList: [],
            propsObj: null
        }
    },
    created() {
        this.$emit('setMenu', 21); // 좌측메뉴 표기
        var today = new Date();
        this.year = today.getFullYear();
        this.month = (today.getMonth() + 1);
    }, methods: {
        getCalendar: function() {
            var tempCal = this.commonUtil.getCalendar(this.year, this.month);
            var weekCnt = this.commonUtil.getWeek(this.year, this.month, 1);
            tempCal.forEach((elem, idx) =>{
                tempCal[idx].unshift(weekCnt + idx);
            });
            this.calendar = tempCal;
        }, openSchedulePop: function(val) {
            if (val == null) {
                return false;
            }
            var obj = new Object();
            obj.componentName = 'putWorkSchedule';
            obj.sYear = this.year;
            obj.sMonth = this.month;
            obj.sDay = val;

            // 수정인지 판단
            var isUpdate = this.scheduleList.some(elem => {
                var sDay = elem.WORKING_START_DATE.split('T')[0];
                var startDay = sDay.split('-');
                sDay = startDay[2];
                var eDay = elem.WORKING_END_DATE.split('T')[0];
                var endDay = eDay.split('-');
                eDay = endDay[2];

                //console.log(sDay + ', ' + eDay + ', ' + val + ', ' + this.month);
                var isOk = false;
                if (Number(endDay[1]) > Number(this.month)) {
                    isOk = (sDay <= val);
                } else if (Number(this.month) > Number(startDay[1])) {
                    isOk = (val <= eDay);
                }
                if (isOk || (sDay <= val && eDay >= val)) {
                    obj.idx = elem.IDX;
                    obj.eYear = endDay[0];
                    obj.eMonth = endDay[1];
                    obj.eDay = endDay[2];
                    obj.sYear = startDay[0];
                    obj.sMonth = startDay[1];
                    obj.sDay = startDay[2];

                    obj.sTime = elem.WORKING_START_TIME;
                    obj.eTime = elem.WORKING_END_TIME;

                    return true;
                }
            });
            
            this.propsObj = obj;
        }, getSchedule: function() {
            var url = '/api/work-schedule/' + this.year + '/' + this.month + '/' + this.$store.getters.getId;
            this.$store.dispatch('getApiCall', url).then(data => {
                this.scheduleList = data;
                this.classObjList = null;
                this.classObjList = this.mappSchedule();
            });
        }, mappSchedule: function() {
            var arrClass = new Array(this.calendar.length);
            for (var x=0; x<arrClass.length; x++) {
                arrClass[x] = new Array(8);
            }
            this.scheduleList.forEach((elem, idx) => {
                var sDay = this.commonUtil.getDateFormat(elem.WORKING_START_DATE);
                var eDay = this.commonUtil.getDateFormat(elem.WORKING_END_DATE);
                var sDayArr = sDay.split('-');
                var eDayArr = eDay.split('-');
                //console.log(sDayArr + '====' + eDayArr + '<----' + elem.WORKING_TYPE);
                //var isYear = eDayArr[0] == this.year? true: false;
                //var isMonth = eDayArr[1] == this.month? true: false;

                var goPaint = false; // 계속 색칠여부 구분
                this.calendar.some((cElem, cIdx) => {
                    return cElem.some((ccElem, ccIdx) => {
                        var isOver = (sDayArr[1] < eDayArr[1]);
                        isOver = !(this.month == sDayArr[1]);

                        if ((ccIdx > 0 && ccElem == sDayArr[2]) || (ccElem == 1 && isOver)) {
                            var cssObj = arrClass[cIdx][ccIdx];
                            if (cssObj == null) {
                                cssObj = new Object(); //css 구조체 생성
                                cssObj.joo1 = false;
                                cssObj.joo = false;
                                cssObj.ya1 = false;
                                cssObj.ya = false;
                                cssObj.printJoo = "";
                                cssObj.printYa = "";
                            }
                            if (elem.WORKING_TYPE == 0) {
                                cssObj.joo1 = true;
                                cssObj.printJoo = elem.WORKING_START_TIME + " ~ " + elem.WORKING_END_TIME;
                            } else if (elem.WORKING_TYPE == 1){
                                cssObj.ya1 = true;
                                cssObj.printYa = elem.WORKING_START_TIME + " ~ " + elem.WORKING_END_TIME;
                            }
                            arrClass[cIdx][ccIdx] = cssObj;
                            goPaint = true;
                        } else if (goPaint && ccIdx > 0){ // 0번 필드 주차 - 제외
                            var cssObj = arrClass[cIdx][ccIdx];
                            if (cssObj == null) {
                                cssObj = new Object(); //css 구조체 생성
                                cssObj.joo1 = false;
                                cssObj.joo = false;
                                cssObj.ya1 = false;
                                cssObj.ya = false;
                            }
                            if (elem.WORKING_TYPE == 0) {
                                cssObj.joo = true;
                            } else if (elem.WORKING_TYPE == 1){
                                cssObj.ya = true;
                            }
                            arrClass[cIdx][ccIdx] = cssObj;
                        }

                        if (ccIdx > 0 && sDayArr[1] == eDayArr[1] && ccElem == eDayArr[2]) {
                            //console.log(ccElem);
                            goPaint = false;
                            return true;
                        }

                        if (isOver && ccElem == eDayArr[2]) {
                            goPaint = false;
                            return true;
                        }
                    });
                });
            });
            
            return arrClass;
        }, init: function() {
            this.classObjList = null;
            this.classObjList = new Array(6);
            for (var x=0; x<this.classObjList.length; x++) {
                this.classObjList[x] = new Array(8);
            }

            this.getCalendar();
            this.getSchedule();
        }
    }, watch: {
        month: function(_newval) {
            var temp = _newval;
            if (temp > 12) {
                this.year++;
                this.month= 1;
            } else if (temp < 1) {
                this.year--;
                this.month=12;
            }
            this.init();
        }
    }
}
</script>