<template>
    <div>
        <b>사용자 관리</b>
        <div class="content_in_start"></div>
        <br>
        <table>
            <thead>
                <tr>
                    <th>번호</th>
                    <th>가입신청일</th>
                    <th>신청자</th>
                    <th>상태</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, idx) in confirmList" v-bind:key="idx" @click="goDetail(item.user_id)">
                    <td>{{item.idx}}</td>
                    <td>{{item.user_confirm_time}}</td>
                    <td>{{item.user_name}}</td>
                    <td>승인대기</td>
                </tr>
            </tbody>
        </table>
        <paging-navi v-if="totSize !== null" :tot-size="totSize" 
            :div-size="pageSize" :curr-page="currPage" @mvPage="mPg => {currPage = mPg}" />
    </div>
</template>

<script>
    import pagingNavi from '../../components/pagingNavi';
    const pageSize = 10;

    export default {
        components: {
            pagingNavi
        },
        data() {
            return {
                pageSize: pageSize,
                currPage : 1,
                totSize: null,
                confirmList: []
            }
        }, created() {
            this.$emit('setMenu', 2); // 좌측메뉴 표기
            this.getTotal();
            this.getList();
            
        }, watch: {
            currPage: function(newval, oldval) {
                this.getList();
            }
        }, methods: {
            getList: function() {
                var url = "/api/user-reg-confirm" + '/' + this.currPage + '/' + pageSize;
                this.$store.dispatch('getApiCall', url).then(data => {
                    var tempList = new Array();
                    data.forEach(x => {
                        x.user_confirm_time = this.commonUtil.getDateFormat(x.user_confirm_time);
                        tempList.push(x);
                    })
                    this.confirmList = tempList;
                });
            },
            goDetail: function(uId) {
                this.$router.push({name: 'userDetailView', params: {mode: 2, userId:uId}});
            },
            getTotal: function() {
                var url = '/api/user-reg-confirm/total-size';
                this.$store.dispatch('getApiCall', url).then(data => {
                    this.totSize = data[0].TOTAL_SIZE;
                });
            }
        }
    }
</script>