import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    countSec: 30 * 60 * 1000,
    actionId: '',
    accessToken: '',
    uId: '',
    uName: '',
    uTeam: '',
    uGroup: '',
    uPosition: '',
    isLoading: false,
  },
  getters: {
    getAccessToken: state => {
      var token = state.uToken;
      if (token == null || token.length < 1) {
        token = sessionStorage.getItem('uToken');
      }
      return token;
    },
    getId: state => {
      var uId = state.uId;
      if (uId == null || uId.length < 1) {
        uId = sessionStorage.getItem('uId');
      }
      return uId;
    },
    getName: state => {
      var uName = state.uName;
      if (uName == null || uName.length < 1) {
        uName = sessionStorage.getItem('uName');
      }
      return uName;
    },
    getTeam: state => {
      return state.uTeam;
    },
    getActId: state => {
      return state.actionId;
    },
    getCountSec: state => {
      return state.countSec;
    },
    getIsLoading: state => {
      return state.isLoading;
    },
  },
  mutations: {
    SET_LOGIN (state, data) {
      state.uToken = data.user_tokenid;
      state.uId = data.user_id;
      state.uName = data.user_name;
      state.uTeam = data.user_team_code;
      state.uGroup = data.user_group_code;
      state.uPosition = data.user_position_code;

      sessionStorage.setItem('uToken',state.uToken);
      sessionStorage.setItem('uId',state.uId);
      sessionStorage.setItem('uName',state.uName);
    },
    LOGOUT (state) {
      state.uToken = '';
      state.uId = '';
      state.uName = '';
      state.uTeam = '';
      state.uGroup = '';
      state.uPosition = '';

      sessionStorage.clear();
    },
    SET_CHECK_ID (state, actId) {
      state.actionId = actId;
    },
    SET_LOADING (state, isLoading) {
      state.isLoading = isLoading;
    }
  },
  actions: {
    resetTime({commit}) {
      clearTimeout(this.getters.getActId);
      var vm = this;
      var actId = setTimeout(function(){
        vm.dispatch('logout');
      }, this.getters.getCountSec, vm);
      commit('SET_CHECK_ID', actId);
    },
    doLogin ({commit}, {userId, userPw}) {
      return axios.post('/auth/login', {userId, userPw}).then((data) => {
        this.dispatch('resetTime');
        console.log(data.data);
        if (data.data.isLogin) {
          commit('SET_LOGIN', data.data.data);
        } 

        return data.data;
      }).catch(err => {
        var obj = new Object();
        obj.isLogin = false;
        obj.message = err.message;

        return obj;
      });
    },
    logout({commit}) {
      commit('LOGOUT');
      location.href = '/loginView';
    },
    getApiCall({commit}, url, payload) {
      //token 없으면 로그아웃 처리
      var token = this.getters.getAccessToken;
      if (token == null || token.length < 1) {
        //this.dispatch('logout');
      }

      this.commit('SET_LOADING', true); //로딩 화면 호출
      axios.defaults.headers.common['x-access-token'] = token;
      this.dispatch('resetTime');
      return axios.get(url, payload).then((data) => {
        this.commit('SET_LOADING', false); //로딩 화면 제거
        return data.data;
      }).catch(error => {
        this.commit('SET_LOADING', false); //로딩 화면 제거
        console.log(error.response.status);
        if (error.response.status == 401) {
          location.href = 'loginView';
        }
      });
      axios.defaults.headers.common['x-access-token'] = undefined;
    },
    postApiCall({commit}, payload) {
      //console.log(payload);
      var url = payload.url;
      if (url == undefined) { //formData 형태 대비 코드 추가
        url = payload.get('url');
      }
      if (url == null || url == undefined || url.length < 1) {
        return false;
      }

      //token 없으면 로그아웃 처리
      var token = this.getters.getAccessToken;
      if (token == null || token.length < 1) {
        this.dispatch('logout');
      }

      this.commit('SET_LOADING', true); //로딩 화면 호출
      axios.defaults.headers.common['x-access-token'] = token;
      this.dispatch('resetTime');
      return axios.post(url, payload).then((data) => {
        this.commit('SET_LOADING', false); //로딩 화면 제거
        return data.data;
      }).catch(error => {
        this.commit('SET_LOADING', false); //로딩 화면 제거
        console.log(error);
        /*
        if (error.response.status == 401) {
          location.href = 'loginView';
        }*/
      });
      axios.defaults.headers.common['x-access-token'] = undefined;
    }
  },
  modules: {
  }
})
