<template>
    <div>
        <textarea v-model="reply"></textarea>
        <button @click="putReply">댓글달기</button>
    </div>
</template>
<script>
    export default{
        data() {
            return {
                reply:null
            };
        }, props: ['idx', 'refIdx', 'appendIdx', 'depth', 'contents'],
        created() {
            if (this.contents != null) {
                this.reply = this.contents;
            }
        },
        methods: {
            putReply: function() {
                var url = '/api/board/proc-reply';
                this.$store.dispatch('postApiCall', {
                    url: url,
                    idx: this.idx,
                    refIdx: this.refIdx,
                    appendIdx: this.appendIdx,
                    depth: this.depth == null? 0: this.depth,
                    contents: this.reply,
                    regId: this.$store.getters.getId,
                    regNm: this.$store.getters.getName
                }).then(res => {
                    console.log(res);
                    alert('댓글이 달렸습니다.');
                    this.reply = null;
                    this.$emit('loadReply');
                });
            }   
        }
    }
</script>