<template>
    <div>
        <b>작업 등록</b>
        <div class="content_in_start"></div>
        <br />
        <table class="form">
            <tr>
                <td style="width:100px;">W/O NO</td>
                <td><input type="text" v-model="dataObj.wrsCode" :readonly="isView"></td>
            </tr>
            <tr>
                <td>의뢰구분</td>
                <td >
                    <input type="text" v-model="dataObj.wrsReqPart" :readonly="isView">
                </td>
            </tr>
            <tr>
                <td>고장유형</td>
                <td>
                    <input type="text" v-model="dataObj.wrsFaultType" :readonly="isView">
                </td>
            </tr>
            <tr>
                <td>의뢰사유</td>
                <td>
                    <input type="text" v-model="dataObj.wrsReqReason" :readonly="isView">
                </td>
            </tr>
            <tr>
                <td>Serial No</td>
                <td><input type="text" v-model="dataObj.wrsEqpSn" name="eqpSn" :readonly="isView"></td>
            </tr>
            <tr>
                <td>Eqp Id</td>
                <td><input type="text" v-model="dataObj.wrsEqpId" name="eqpId" :readonly="isView"></td>
            </tr>
            <tr>
                <td>의뢰제목</td>
                <td><input type="text" v-model="dataObj.wrsTitle" :readonly="isView"></td>
            </tr>
            <tr>
                <td>현장담당자</td>
                <td><input type="text" v-model="dataObj.wrsWorkerName" :readonly="isView"></td>
            </tr>
            <!-- tr>
                <td>연락처</td>
                <td><input type="text" v-model="dataObj.title" :readonly="isView"></td>
            </tr -->
            <!-- tr>
                <td>의뢰일자</td>
                <td><input type="datetime-local" v-model="reqDay" :readonly="isView"></td>
            </tr -->
        </table>
        <br>
        <div class="btn" @click="checkRegister" v-if="!isView">작업 등록</div>
        <div class="btn" @click="isView = false" v-if="isView">작업 수정</div>
        <div class="btn" @click="goHist">이력 보기</div>
        <div class="btn" @click="openAccount" v-if="isView">정산 입력</div>

        <msgbox :title="boxObj.title" :message="boxObj.msg" :boxType="boxObj.bType" v-if="showMsgBox != null && showMsgBox" @close="showMessagebox" @callback="procRegister" />
        <accountPop :workCode="openPop" v-if="openPop != null" @close="val => {popWorkCode = val}" />
    </div>
</template>

<script>
    import dataSelectBox from '@/components/dataSelectBox';
    import msgbox from  '@/components/messageBox';
    import accountPop from '@/components/accountInputPop';
    export default{
        components: {
            dataSelectBox, msgbox, accountPop
        },
        data() {
            return {
                dataObj: {
                    workCode: null,
                    userId: this.$store.getters.getName,
                    wrsCode: null,
                    wrsReqPart: null,
                    wrsFaultType: null,
                    wrsReqReason: null,
                    wrsLine: null,
                    wrsProcess: null,
                    wrsEqpId: null,
                    wrsEqpSn: null,
                    wrsChamberNm: null,
                    wrsTitle: null,
                    wrsWorkerName: null,
                    wrsReqDay: null,
                    //wrsExpectDay: null,
                    url: null
                },
                boxObj: {
                    title: null,
                    msg: null,
                    bType: null
                },
                reqDay: null,
                //expectDay: null,
                popWorkCode: null,
                isShow: false,
                isView: false
            };
        }, props: ['propsObj']
        , created() {
            this.$emit('setMenu', 15); // 좌측메뉴 표기
            if (this.propsObj != null && this.propsObj.WORK_CODE != null) {
                this.isView = true;
                this.mappingData();
                var sendObj = this.propsObj.searchCond == null? null:this.propsObj.searchCond;
                this.$emit('setProps', sendObj);
            }
        },
        mounted() {
            var tempInput = document.querySelectorAll('input');
            tempInput.forEach((elem, idx) => {
                if (elem.value == 'null') {
                    tempInput[idx].value = '';
                }
            });
        }, computed: {
            showMsgBox: function() {
                return this.isShow;
            },
            openPop: function() {
                return this.popWorkCode;
            }
        }, methods: {
            checkRegister: function() {
                if (this.dataObj.wrsEqpId == null || this.dataObj.wrsEqpId.length < 1) {
                    //document.getElementsByName('eqpId')[0].focus();
                    this.showMessagebox(true, 'alert', '작업 등록', 'EQP ID 가 입력되지 않았습니다.');
                    return false;
                }
                if (this.dataObj.wrsTitle == null || this.dataObj.wrsTitle.length < 1) {
                    this.showMessagebox(true, 'alert', '작업 등록', '의뢰제목이 입력되지 않았습니다.');    
                    return false;
                }
                this.showMessagebox(true, 'confirm', '작업 등록', '작업을 등록하시겠습니까?');
            },
            procRegister: function() {
                this.dataObj.url = '/api/proc/put-wrs-work';
                if (this.reqDay != null) {
                    this.dataObj.wrsReqDay = this.commonUtil.dateTimeTransServer(this.reqDay);
                }
                /*
                if (this.expectDay != null) {
                    this.dataObj.wrsExpectDay = this.commonUtil.dateTimeTransServer(this.expectDay);
                }
                */
                this.$store.dispatch('postApiCall', this.dataObj).then(data => {
                    if (!data.isSuccess) {
						this.showMessagebox(true, 'alert', '작업 등록 실패', data.message);
						return;
					} else {
						this.showMessagebox(true, 'alert', '작업 등록', '작업 등록이 완료되었습니다.');
                        this.clearAll();
						return;
					}
                });
            },
            showMessagebox: function(val, mode, title, msg) {                
				this.boxObj.bType = mode;
				this.boxObj.title = title;
				this.boxObj.msg = msg;
				this.isShow = val;
            },
            goHist: function() {
                this.$router.push('/wrsCodeHist');
            }, clearAll: function() {
                this.dataObj.wrsCode = null;
                this.dataObj.wrsReqPart = null;
                this.dataObj.wrsFaultType = null;
                this.dataObj.wrsReqReason = null;
                //this.dataObj.wrsLine = null;
                //this.dataObj.wrsProcess = null;
                this.dataObj.wrsEqpId = null;
                this.dataObj.wrsEqpSn = null;
                //this.dataObj.wrsChamberNm = null;
                this.dataObj.wrsTitle = null;
                this.dataObj.wrsWorkerName = null;
                this.dataObj.wrsReqDay = null;
                //this.dataObj.wrsExpectDay = null;
                this.dataObj.url = null;
                this.reqDay = null;
                //this.expectDay = null;
            }, mappingData: function() {
                this.dataObj.wrsCode = this.propsObj.WRS_CODE == null? '':this.propsObj.WRS_CODE;
                this.dataObj.wrsReqPart = this.propsObj.WRS_REQUEST_PART == null? '':this.propsObj.WRS_REQUEST_PART;
                this.dataObj.wrsFaultType = this.propsObj.WRS_FAULT_TYPE == null? '':this.propsObj.WRS_FAULT_TYPE;
                this.dataObj.wrsReqReason = this.propsObj.WRS_REQUEST_REASON == null? '':this.propsObj.WRS_REQUEST_REASON;
                this.dataObj.wrsLine = this.propsObj.WORK_LINE_NAME == null? '':this.propsObj.WORK_LINE_NAME;
                this.dataObj.wrsProcess = this.propsObj.WRS_PROCESS == null? '':this.propsObj.WRS_PROCESS;
                this.dataObj.wrsEqpId = this.propsObj.WRS_EQP_ID == null? '':this.propsObj.WRS_EQP_ID;
                this.dataObj.wrsEqpSn = this.propsObj.WRS_EQP_SN == null? '':this.propsObj.WRS_EQP_SN;
                this.dataObj.wrsChamberNm = this.propsObj.WRS_CHAMBER_NAME == null? '':this.propsObj.WRS_CHAMBER_NAME;
                this.dataObj.wrsTitle = this.propsObj.WRS_REQ_TITLE == null? '':this.propsObj.WRS_REQ_TITLE;
                this.dataObj.wrsWorkerName = this.propsObj.WRS_WORK_PERSON_NAME == null? '':this.propsObj.WRS_WORK_PERSON_NAME;
                this.dataObj.wrsReqDay = this.propsObj.WRS_REQ_DAY == null? '':this.propsObj.WRS_REQ_DAY;
                //this.dataObj.wrsExpectDay = this.propsObj.WRS_EXPECT_DAY == null? '':this.propsObj.WRS_EXPECT_DAY;
                this.dataObj.workCode = this.propsObj.WORK_CODE == null? '':this.propsObj.WORK_CODE;

                //this.reqDay = this.commonUtil.dateTimeTransVue(this.propsObj.WRS_REQ_DAY);
                //this.expectDay = this.commonUtil.dateTimeTransVue(this.propsObj.WRS_EXPECT_DAY);
            },
            openAccount: function() {
                this.popWorkCode = this.dataObj.workCode;
            }
        }
    }
</script>