<template>
    <div>
        <b>그룹코드 관리</b>
        <div class="content_in_start"></div>
        <div class="work_btn" @click="openArea(0)">그룹코드 등록</div>
        <table>
            <thead>
                <tr>
                    <th style="width:calc(29%)">팀이름</th>
                    <th style="width:calc(25%)">그룹코드</th>
                    <th style="width:calc(29%)">그룹이름</th>
                    <th style="width:calc(15%)">처리구분</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item, idx) in groupCodeList" v-bind:key="idx">
                    <td>{{item.WORK_TEAM_CODE}}</td>
                    <td>{{item.WORK_GROUP_CODE}}</td>
                    <td>{{item.WORK_GROUP_NAME}}</td>
                    <td>
                        <div class="tdbtn" style="width:40px;" @click="openArea(1, idx)">수정</div>
                        <div class="tdbtn" style="width:40px;margin-left:7px" @click="delCheck(idx)">삭제</div>
                    </td>
                </tr>
            </tbody>
        </table>
        <div v-if="openInput">
            <br />
            <table>
                <tr>
                    <td style="width:calc(29%)">
                        <dataSelectBox v-model="objData.teamCd" :items="list.teamCode" :input_id="'teamCode'" @input="val => {objData.teamCd = val}" 
                            :viewText="'팀 선택'" :setVal="objData.teamCd" /> 
                    </td>                
                    <td style="width:calc(25%)"><input type="text" v-model="objData.groupCd" placeholder="그룹코드" :readonly="this.objData.userFrom != 0"></td>
                    <td style="width:calc(29%)"><input type="text" v-model="objData.groupNm" placeholder="그룹이름"></td>
                    <td style="width:calc(12%)">
                        <div class="tdbtn" style="width:40px;" @click="checkValid">저장</div>
                        <div class="tdbtn" style="width:40px;margin-left:7px" @click="closeArea">닫기</div>
                    </td>
                </tr>
            </table>
        </div>
        <msgbox :title="boxObj.title" :message="boxObj.msg" :boxType="boxObj.bType" v-if="showMsgBox != null && showMsgBox" @close="showMessagebox" @callback="procRegister" />
    </div>
</template>
<script>
    import dataSelectBox from '@/components/dataSelectBox';
    import msgbox from  '@/components/messageBox';
    export default {
        components: {
            dataSelectBox, msgbox
        },
        data() {
            return {
                list: {
                    teamCode: [],
                    eqpType: [
                        {val:0, title:'스크러버'},
                        {val:1, title:'칠러'}
                    ]
                },
                objData: {
                    userFrom: 0,
                    teamCd: null,
                    groupCd: null,
                    groupNm: null,
                    eqpType: null,
                    userId: this.$store.getters.getId
                },
                boxObj: {
                    title: null,
                    msg: null,
                    bType: null
                },
                groupCodeList: [],
                openInput: false,
                isShow: false
            };
        },
        created() {
            this.$emit('setMenu', 82); // 좌측메뉴 표기
            this.getList();
        },
        computed: {
            showMsgBox: function() {
                return this.isShow;
            }
        },
        methods: {
            getList: function() {
                var url = "/api/common/group-code";
                this.$store.dispatch('getApiCall', url).then(data => {
                    this.groupCodeList = data;
                });
            },
            openArea: async function(mode, idx) {
                this.closeArea();
                
                this.objData.userFrom = mode;
                try {
                    this.$store.commit('SET_LOADING', true); //로딩 화면 호출

                    if (this.list.teamCode.length < 1) {
                        this.commonUtil.getTeamCode().then(data => {
                            this.list.teamCode = data;
                        });
                    }

                    if (idx != null) {
                        this.objData.teamCd = this.groupCodeList[idx].WORK_TEAM_CODE;
                        this.objData.groupCd = this.groupCodeList[idx].WORK_GROUP_CODE;
                        this.objData.groupNm = this.groupCodeList[idx].WORK_GROUP_NAME;
                        this.objData.eqpType = this.groupCodeList[idx].TEAM_EQP_TYPE;
                    }
                    this.openInput = true;
                } catch(err) {
                    console.log(err.message);
                } finally {
                    this.$store.commit('SET_LOADING', false); //로딩 화면 닫음
                }
            },
            closeArea: function() {
                this.objData = {
                    userFrom: 0,
                    teamCd: null,
                    groupCd: null,
                    groupNm: null,
                    eqpType: null,
                    userId: this.$store.getters.getId
                }

                this.openInput = false;
            },
            checkValid: function() {
                if (this.objData.groupCd == null || this.objData.groupCd.length < 1) {
                    this.showMessagebox(true, 'alert', '데이터 확인', '그룹 코드가 입력되지않았습니다.');
                    return false;
                }
                if (this.objData.groupNm == null || this.objData.groupNm.length < 1) {
                    this.showMessagebox(true, 'alert', '데이터 확인', '그룹 이름이 입력되지않았습니다.');
                    return false;
                }

                if (this.objData.userFrom == 0) {
                    this.showMessagebox(true, 'confirm', '등록', '그룹코드를 등록하시겠습니까?');
                } else if (this.objData.userFrom == 1) {
                    this.showMessagebox(true, 'confirm', '수정', '그룹코드 정보를 수정하시겠습니까?');
                } else {
                    return false;
                }
            },
            delCheck: function(idx) {
                this.objData.userFrom = 2;
                this.objData.teamCd = this.groupCodeList[idx].WORK_TEAM_CODE;
                this.objData.groupCd = this.groupCodeList[idx].WORK_GROUP_CODE;
                this.objData.groupNm = this.groupCodeList[idx].WORK_GROUP_NAME;
                this.objData.eqpType = this.groupCodeList[idx].TEAM_EQP_TYPE;

                this.showMessagebox(true, 'confirm', '삭제', this.objData.groupCd + '그룹코드를 삭제하시겠습니까?');
            },
            procRegister: function() {
                this.objData.url = "/api/proc/put-group-code";
                this.$store.dispatch('postApiCall', this.objData).then(data => {
                    if (!data.isSuccess) {
						this.showMessagebox(true, 'alert', '요청 실패', data.message);
						return;
					} else {
                        if (this.objData.userFrom == 0) {
                            this.showMessagebox(true, 'alert', '그룹코드 등록', '그룹코드 등록이 완료되었습니다.');
                        } else if (this.objData.userFrom == 1) {
                            this.showMessagebox(true, 'alert', '그룹코드 수정', '그룹코드 수정이 완료되었습니다.');
                        }
                        this.closeArea();
                        this.getList();
						return;
					}
                })
            },
            showMessagebox: function(val, mode, title, msg) {                
				this.boxObj.bType = mode;
				this.boxObj.title = title;
				this.boxObj.msg = msg;
				this.isShow = val;
            }
        }
    }
</script>

<style type="less">
    .content_in table td select {
        width:calc(100% - 10px);padding-left:15px
    }
    .content_in table td select:disabled {
        width:calc(100% - 10px);padding-left:15px
    }
</style>