<script>
    import msgbox from  '@/components/messageBox';
    import dataSelectBox from '@/components/dataSelectBox';
    export default{
        components: {
            msgbox, dataSelectBox
        },
        data() {
            return {
                dataObj: null,
                boxObj: {
                    title: null,
                    msg: null,
                    bType: null
                },
                comboObj: [
                    {title:'Y', val:1},
                    {title:'N', val:0},
                ],
                isShow: false
            };
        },
        created() {
            this.dataObj = new Object();
            this.getData();
        },
        props: ['workCode'],
        methods: {
            close: function() {
                this.$emit('close');
            },
            getData: function() {
                var url = "/api/wrs-account-list?workCode=" + this.workCode;
                this.$store.dispatch('getApiCall', url).then(data => {
                    data[0].WRS_PREV_WORKING_DATE = this.commonUtil.getDateFormat(data[0].WRS_PREV_WORKING_DATE);
                    data[0].WRS_WORKING_START_TIME = this.commonUtil.dateTimeTransServer(data[0].WRS_WORKING_START_TIME);
                    data[0].WRS_WORKING_END_TIME = this.commonUtil.dateTimeTransServer(data[0].WRS_WORKING_END_TIME);
                    data[0].EQP_SCR_CARRY_DATE = this.commonUtil.getDateFormat(data[0].EQP_SCR_CARRY_DATE);
                    data[0].EMS_REGISTER_DATE = this.commonUtil.getDateFormat(data[0].EMS_REGISTER_DATE);
                    this.dataObj = data[0];
                });
            },
            checkRegister: function() {
                if (this.dataObj.WORK_CODE == null || this.dataObj.WORK_CODE.length < 1) {
                    this.showMessagebox(true, 'alert', '정산 등록', '작업 코드가 입력되지 않았습니다.');
                    return false;
                }
                this.showMessagebox(true, 'confirm', '정산 등록', '정산을 등록하시겠습니까?');
            }, procRegister: function() {
                this.dataObj.REC_ID = this.$store.getters.getId;
                this.dataObj.url = '/api/proc/put-wrs-account';

                this.$store.dispatch('postApiCall', this.dataObj).then(data => {
                    if (!data.isSuccess) {
						this.showMessagebox(true, 'alert', '정산 등록 실패', data.message);
						return;
					} else {
						this.$parent.showMessagebox(true, 'alert', '정산 등록', '정산 등록이 완료되었습니다.');
                        this.close();
						return;
					}
                });
            },
            showMessagebox: function(val, mode, title, msg) {                
				this.boxObj.bType = mode;
				this.boxObj.title = title;
				this.boxObj.msg = msg;
				this.isShow = val;
            }
        },
        computed: {
            showMsgBox: function() {
                return this.isShow;
            }
        }
    }
</script>

