<template>
    <div>
        <div>
            <ul>
                <li v-for="(item, idx) in replyList" v-bind:key="idx">
                    <div class="perReply">
                        <span :style="{width:item.DEPTH * 30 + 'px'}">&nbsp;</span>
                        <span class="contents">
                            {{item.REG_NM}}<br />
                            {{item.CONTENTS}}
                        </span>
                        <span class="btn">
                            <button @click="toggleReply(idx, 'U')">수정</button>
                            <button @click="toggleReply(idx, 'A')">댓글</button>
                        </span>
                    </div>
                    <div v-if="replyList[idx].addReply">
                        <putReply :refIdx="refIdx" :depth="item.DEPTH + 1" :appendIdx="item.IDX" v-on:loadReply="getReply" />            
                    </div>
                    <div v-if="replyList[idx].upReply">
                        <putReply :idx="item.IDX" :contents="item.CONTENTS" v-on:loadReply="getReply" />
                    </div>
                </li>
            </ul>
        </div>
        <putReply :refIdx="refIdx" v-on:loadReply="getReply" />
    </div>
</template>
<script>
    import putReply from './replyInput';
    export default{
        components: {
            putReply
        },
        data() {
            return {
                replyList: []
            };
        }, props : ['refIdx'],
        created() {
            this.getReply();
        }, methods: {
            getReply: function() {
                var url = "/api/board/reply/" + this.refIdx;
                this.$store.dispatch('getApiCall', url).then((data) => {
                    var tempList = data;
                    this.replyList = this.sortReply(tempList);
                });
            }, sortReply: function(sortList) {
                var tempList = new Array();
                sortList.forEach((elem, idx) => {
                    elem.addReply = false;
                    elem.upReply = false;
                    var currDepth = elem.DEPTH;
                    if (elem.DEPTH > 0) {
                        tempList.some((x, x_idx) => {
                            if (x.IDX == elem.APPEND_IDX) {
                                elem.childCnt = 0;
                                tempList[x_idx].childCnt++;
                                tempList.splice(x_idx + tempList[x_idx].childCnt, 0, elem);
                                return true;
                            }
                        });
                    } else {
                        elem.childCnt = 0;
                        tempList.push(elem);
                    }
                });
                return tempList;
            }, toggleReply: function(pos, mode) {
                if (mode == 'U') {
                    this.replyList[pos].addReply = false;
                    if (this.replyList[pos].upReply) {
                        this.replyList[pos].upReply = false;
                    } else {
                        this.replyList[pos].upReply = true;
                    }
                } else {
                    this.replyList[pos].upReply = false;
                    if (this.replyList[pos].addReply) {
                        this.replyList[pos].addReply = false;
                    } else {
                        this.replyList[pos].addReply = true;
                    }
                }
            }
        }
    }
</script>

<style lang="less">
    .perReply {
        width: 600px;
        border:1px solid blue;
        text-align:left;
        span {
            display:inline-block;
            width:calc(20%);
        }
        span.contents {
            width:calc(60%);
        }
        span.btn{
            float:right;
        }
    }
</style>