<template>
    <div class="day_control">
		<div class="dark_wall"></div>
		<div class="day_start_end_pop">
			<b>일정조정</b><br><br>

			<input type="date" placeholder="날짜선택" v-model="s_date" :disabled="idx == null"> ~ <input type="date" placeholder="날짜선택" v-model="e_date"><br>
			<div class="time_picker">
				<div class="time_picker_cell">
					<div class="time_picker_1">
						<div class="time_up" @click="s_time++"><img src="@/assets/img/up.png" alt="" ></div>
						<span><input type="text" style="width:50%;text-align:center;border:1px solid #d4d4d4;padding:0px;font-size:20px;" v-model="s_time"></span>
						<div class="time_down" @click="s_time--"><img src="@/assets/img/down.png" alt="" ></div>
					</div>
					<div class="time_picker_2">
						<div class="time_up"></div>
						<span><input type="text" style="width:50%;text-align:center;border:1px solid #d4d4d4;padding:0px;font-size:20px;" v-model="s_minute" readonly></span>
						<div class="time_down"></div>
					</div>
					<div class="time_picker_3">
						<div class="time_up"></div>
						<span><input type="text" style="width:50%;text-align:center;border:1px solid #d4d4d4;padding:0px;font-size:20px;" v-model="s_second" readonly></span>
						<div class="time_down"></div>
					</div>
				</div>
				<div class="time_picker_cell_mid">~</div>
				<div class="time_picker_cell">
					<div class="time_picker_1">
						<div class="time_up" @click="e_time++"><img src="@/assets/img/up.png" alt=""></div>
						<span><input type="text" style="width:50%;text-align:center;border:1px solid #d4d4d4;padding:0px;font-size:20px;" v-model="e_time"></span>
						<div class="time_down" @click="e_time--"><img src="@/assets/img/down.png" alt=""></div>
					</div>
					<div class="time_picker_2">
						<div class="time_up"></div>
						<span><input type="text" style="width:50%;text-align:center;border:1px solid #d4d4d4;padding:0px;font-size:20px;" v-model="e_minute" readonly></span>
						<div class="time_down"></div>
					</div>
					<div class="time_picker_3">
						<div class="time_up"></div>
						<span><input type="text" style="width:50%;text-align:center;border:1px solid #d4d4d4;padding:0px;font-size:20px;" v-model="e_second" readonly></span>
						<div class="time_down"></div>
					</div>
				</div>
			</div>

			<div class="day_start_end_pop_btn" @click="saveWorkSchedule">결정</div>
			<div class="day_start_end_pop_x" @click="close">닫기</div>			
		</div>
		<msgbox :title="boxTitle" :message="boxMsg" :boxType="boxType" v-if="showMsgBox != null && showMsgBox" @close="showMessagebox" @callback="putWorkSchedule" />
	</div>
</template>
<script>
	import msgbox from '@/components/messageBox';
    export default {
		name: 'putWorkScheduleView',
		components: {
			msgbox
		},
        data() {
            return {
				boxTitle: null,
				message: null,
				boxType: null,
				isShow: false,
				s_time: '00',
				s_minute: '00',
				s_second: '00',
				s_date: null,
				e_time: '00',
				e_minute: '00',
				e_second: '00',
				e_date: null,
				idx: null,
				workType: 0, // 근무형태 0.주간 1.야간
				workStatus: 0 // 0: 근무계획신청, 1: 근무계획 수정, 2: 관리자 승인, 3: 관리자 반려
			};
		}, props: ['propsObj'],
		created() {
			// 기초값 세팅
			var s_year = this.propsObj.sYear;
			var s_month = Number(this.propsObj.sMonth) < 10? '0'+Number(this.propsObj.sMonth) : this.propsObj.sMonth;
			var s_day = Number(this.propsObj.sDay) < 10? '0'+Number(this.propsObj.sDay) : this.propsObj.sDay;
			this.s_date = s_year + '-' + s_month + '-' + s_day;

			var e_year = this.propsObj.eYear == null? this.propsObj.sYear: this.propsObj.eYear;
			var e_month = this.propsObj.eMonth == null? this.propsObj.sMonth : this.propsObj.eMonth;
			var e_month = Number(e_month) < 10? '0'+Number(e_month) : e_month;
			var e_day = this.propsObj.eDay == null? this.propsObj.sDay : this.propsObj.eDay;
			var e_day = Number(e_day) < 10? '0'+Number(e_day) : e_day;
			this.e_date = e_year + '-' + e_month + '-' + e_day;

			console.log(this.propsObj.sTime);
			if (this.propsObj.sTime != null) {
				this.s_time = this.propsObj.sTime.split(':')[0];
				this.s_time = Number(this.s_time) < 10?  '0' + this.s_time:this.s_time;
			}

			if (this.propsObj.eTime != null) {
				this.e_time = this.propsObj.eTime.split(':')[0];
				this.e_time = Number(this.e_time) < 10?  '0' + this.e_time:this.e_time;
			}

			this.idx = this.propsObj.idx;
		},
        methods: {
            close: function() {
                this.$emit('close', null);
			}, putWorkSchedule: function() {
				var sendObj = new Object();
				sendObj.url = '/api/proc/put-work-schedule';
				sendObj.workType = this.workType;
				sendObj.userId = this.$store.getters.getId;
				sendObj.startDate = this.s_date;
				sendObj.startTime = this.s_time + ':' + this.s_minute + ':' + this.s_second;
				sendObj.endDate = this.e_date;
				sendObj.endTime = this.e_time + ':' + this.e_minute + ':' + this.e_second;
				if (this.idx != null) {
					sendObj.idx = this.idx;
					this.workStatus = 1;
				}
				sendObj.userFrom = this.workStatus;
				if (Number(this.s_time) < 22 && Number(this.s_time) > 6) { //주간 근무 표기
					sendObj.workType = 0;
				} else {
					sendObj.workType = 1;
				}

				this.$store.dispatch('postApiCall', sendObj).then(res => {
					if (!res.isSuccess) {
						this.showMessagebox(true, 'alert', '근무 승인 요청 실패', res.message);
						return;
					} else {
						this.showMessagebox(true, 'alert', '근무 승인 요청', '근무 등록 요청이 완료되었습니다.');
						this.$emit('reload');
						this.close();
						return;
					}
				});
			}, showMessagebox: function(val, mode, title, msg) {                
				this.boxType = mode;
				this.boxTitle = title;
				this.boxMsg = msg;

				this.isShow = val;
            }, saveWorkSchedule: function() {
				var sDate = new Date(this.s_date);
				var eDate = new Date(this.e_date);

				if (eDate < sDate) {
					this.showMessagebox(true, 'alert', '알림', '시작일 보다 종료일이 작을 수 없습니다.');
					return false;
				}

				if (Number(this.s_time) == Number(this.e_time)) {
					this.showMessagebox(true, 'alert', '알림', '근무 시작시간과 종료시간이 동일합니다.');
					return false;
				}

				var title = "승인 요청";
				var msg = "지정된 근무 승인을 요청하시겠습니까?"
				this.showMessagebox(true, 'confirm', title, msg);
			}
        }
		, computed: {
            showMsgBox: function() {
                return this.isShow;
            }
        }, watch: {
			s_time:function() {
				if(Number(this.s_time) > 23 ) {
					this.s_time = '00';
				} else if (Number(this.s_time) < 0) {
					this.s_time = '23';
				}  else if (Number(this.s_time) < 10) {
					this.s_time = '0' + Number(this.s_time);
				} else if (Number(this.s_time) > 9){
					this.s_time = Number(this.s_time);
				}
			}, e_time:function() {
				if(Number(this.e_time) > 23) {
					this.e_time = '00';
				} else if (Number(this.e_time) < 0) {
					this.e_time = '23';
				}  else if (Number(this.e_time) < 10) {
					this.e_time = '0' + Number(this.e_time);
				} else if (Number(this.e_time) > 9){
					this.e_time = Number(this.e_time);
				}
			}, s_date: function() {
				//console.log(this.s_date);
			}

		}
    }
</script>