<template>
    <select v-model='selVal' v-bind:id="input_id" v-on:change="updateValue(selVal)">
         <option value="" v-if="viewText != null" :selected="setVal == ''">{{viewText}}</option>
        <option v-for="(item, idx) in items" :value="item.val" v-bind:key="idx" :selected="setVal == item.val">{{ item.title }}</option>
    </select>
</template>

<script>
    export default{
        name: "dataSelectBox",
        data() {
            return {
                selVal: ""
            };
        },
        props: ['setVal', 'items', 'input_id', 'viewText'],
        methods: {
            updateValue: function(value) {
                this.$emit('input', value);
            }
        },
        watch: {
            setVal: function(newval,oldval) {
                this.selVal = newval;
            }
        },
        created() {
            this.selVal = this.setVal == null? '':this.setVal;
        }
    }
</script>