<template>
    <div class="wrap">
        <header>
			<a href="#" class="logo" @click="logout"></a>
			<div class="member_rel_btn" @click="logout">로그아웃</div>
			<div class="member_rel_btn" @click="goUserInfo">회원정보</div>
			<span class="membder_name">{{userNm}}</span>
		</header>
		<leftmenu :pMenuNum="pMenuNum" />
		<div class="content">
			<div class="content_in" >
                <slot />
            </div>
		</div>
        <div id="loading" v-if="showLoad">
		    <div class="dark_wall"></div>
		    <div class="messagebox_loading">	
			    <img src="@/assets/img/loading.gif" style="width:100px;"><br><br>&nbsp;&nbsp;&nbsp;&nbsp;LOADING....
		    </div>
	    </div>
	</div>
</template>

<script>
    import leftmenu from '@/components/leftmenu';
    export default {
        name: 'mainLayout',
        components: {
            leftmenu
        },
        data() {
            return {
                userNm: this.$store.getters.getName,
            }
        }, props: ['pMenuNum', 'propsObj'],
        computed: {
            showLoad: function() {
                return this.$store.getters.getIsLoading;
            }
        }, methods: {
            logout: function() {
                this.$store.dispatch('logout');
            }, goUserInfo:function() {
                this.$router.push({name: 'userDetailView', params: {mode:5, userId: this.$store.getters.getId}})
            }
        }
    }
</script>