<template>
    <div class="wrap">
		<header>
			<a href="#" class="logo"></a>
		</header>
		<div class="content join_content">
			<div class="content_in">
                <slot />
            </div>
		</div>
        <div id="loading" v-if="showLoad">
		    <div class="dark_wall"></div>
		    <div class="messagebox_loading">	
			    <img src="@/assets/img/loading.gif" style="width:100px;"><br><br>&nbsp;&nbsp;&nbsp;&nbsp;LOADING....
		    </div>
	    </div>
        <div id="popup" v-if="showMsg" >
            <div class="dark_wall"></div>
            <div class="messagebox_loading_message">
                <div class="messagebox_loading_x">X</div>
                {{printMsg}}
            </div>
	    </div>
	</div>
</template>

<script>
    export default {
        name: 'clearLayout',
        data() {
            return {
                showMsg: false,
                printMsg: null
            }
        }, computed: {
            showLoad: function() {
                return this.$store.getters.getIsLoading;
            }
        }, methods: {
            closeMsgbox: function() {
                this.printMsg = null;
                this.showMsg = false;
            }, showMsgbox: function(msg) {
                console.log('recv msg : ' + msg);
                this.printMsg = msg;
                this.showMsg = true;
            }
        }
    }
</script>