<template>
    <div>
        <b>점검 현황</b>
        <div class="content_in_start"></div>
        <dataSelectBox v-model="mode" :items="modeList" :input_id="'mode'" @input="val => {mode = val}" :setVal="mode" /> 
        <input type="date" placeholder="날짜선택" v-model="targetDate" style="height:40px;line-height:40px">
        <br />
        <dataSelectBox v-model="placeCd" :items="placeList" :input_id="'placeCode'" @input="val => {placeCd = val}" :viewText="'사업장 선택'" /> 
        <dataSelectBox v-model="partCd" :items="partList" :input_id="'partCode'" @input="val => {partCd = val}" :viewText="'사업부 선택'" 
            :disabled="partList.length < 1" :setVal="partCd" /> 
        <dataSelectBox v-model="complexCd" :items="complexList" :input_id="'complexCode'" @input="val => {complexCd = val}" :viewText="'단지 선택'" 
            :disabled="complexList.length < 1" :setVal="complexCd" /> 
        <dataSelectBox v-model="lineCd" :items="lineList" :input_id="'lineCode'" @input="val => {lineCd = val}" :viewText="'라인 선택'" 
            :disabled="lineList.length < 1" :setVal="lineCd" /> 
        <div class="search_btn" @click="search">검색</div>
        <br /><br />
        <div v-if="checkList.length > 0">
            <table>
                <thead>
                    <tr>
                        <th>총점검대수</th>
                        <th>점검대수</th>
                        <th>누락대수</th>
                        <th>점검율</th>
                        <th>전체점검시간</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item, idx) in checkList" v-bind:key="idx">
                        <td>{{item.TOTAL_CHECK_NUMBER_OF_EQP}}</td>
                        <td>{{item.CHECK_NUMBER_OF_EQP}}</td>
                        <td @click="openPop(idx)">{{item.MISS_CHECK_NUMBER_OF_EQP}}</td>
                        <td>{{item.CHECK_RATE}} <span style="font-size:13px;" v-if="item.CHECK_RATE != null"> %</span></td>
                        <td>{{item.TOTAL_CHECK_TIME}} <span style="font-size:13px;" v-if="item.TOTAL_CHECK_TIME != null"> 분</span></td>
                    </tr>
                </tbody>
            </table>
            <paging-navi v-if="totSize !== null" :tot-size="chgTotal" :div-size="pageSize" :curr-page="currPage" @mvPage="mPg => {currPage = mPg}" />
        </div>
        <div v-else>
            <span>조회된 데이터가 없습니다.</span>
        </div>
        <msgbox :title="boxObj.title" :message="boxObj.msg" :boxType="boxObj.bType" v-if="showMsgBox != null && showMsgBox" @close="showMessagebox" />
        <missPop :propsObj="missPopObj" v-if="missPopObj != null" @close="closePop" />
    </div>
</template>

<script>
    import pagingNavi from '@/components/pagingNavi';
    import dataSelectBox from '@/components/dataSelectBox';
    import msgbox from '@/components/messageBox';
    import missPop from '@/components/missCheckListPop';
    const pageSize = 10;

    export default{
        components: {
            pagingNavi, dataSelectBox, msgbox, missPop
        },
        data() {
            return {
                pageSize: pageSize,
                currPage : 1,
                totSize: null,
                placeCd: null,
                partCd: null,
                complexCd: null,
                lineCd: null,
                targetDate: null,
                mode: null,
                checkList: [],
                placeList: [],
                partList: [],
                complexList: [],
                lineList: [],
                modeList: [],
                isShow: false,
                boxObj: {
                    title: null,
                    msg: null,
                    bType: null
                }, 
                missPopObj: null
            };
        }, methods: {
            search: function() {
                if (this.targetDate == null || this.targetDate.length < 1 || this.lineCd == null || this.lineCd.length < 1) {
                    this.showMessagebox(true, 'alert', '알 림', '일자 또는 대상 라인이 선택되지 않았습니다.');
                    return false;
                }

                this.currPage = 1;
                this.getTotal();
                this.getList();
            },
            getList: function() {
                var url = "";
                if (this.lineCd != null || this.lineCd.length > 0) {
                    url = "/api/check-status-line/" + this.targetDate + "/" + this.lineCd;
                }
                var query = "?mode=" + this.mode + "&pageNo=" + this.currPage + "&pageSize=" + pageSize;
                url += query;
                
                this.$store.dispatch('getApiCall', url).then(data => {
                    this.checkList = data;
                });
            },
            getTotal: function() {
                var url = "";
                if (this.lineCd != null || this.lineCd.length > 0) {
                    url = "/api/check-status-line/total-size/" + this.targetDate + "/" + this.lineCd;
                }
                var query = "?mode=" + this.mode;
                url += query;

                this.$store.dispatch('getApiCall', url).then(data => {
                    this.totSize = data[0].TOTAL_SIZE;
                });
            },
            clearValue: function(step) {
                this.lineCd = '';
                this.lineList = [];
                if (step == 'complex') {
                    return false;
                }

                this.complexCd = '';
                this.complexList = [];
                if (step == 'part') {
                    return false;
                }

                this.partCd = '';
                this.partList = [];
                if (step == 'place') {
                    return false;
                }

                this.placeCd = '';
            },
            showMessagebox: function(val, mode, title, msg) {                
				this.boxObj.bType = mode;
				this.boxObj.title = title;
				this.boxObj.msg = msg;
				this.isShow = val;
            },
            setModeList: function () {
                var tempList = [];
                var elem = new Object();
                elem.val = 0;
                elem.title = '야간점검';
                tempList.push(elem);

                elem = new Object();
                elem.val = 1;
                elem.title = '감독관점검';
                tempList.push(elem);

                elem = new Object();
                elem.val = 2;
                elem.title = '관리자점검';
                tempList.push(elem);

                this.modeList = tempList;
            },
            openPop: function(idx) {
                if (this.checkList[idx].MISS_CHECK_NUMBER_OF_EQP < 1) {
                    return false;
                }
                var obj = new Object();
                obj.CHECK_TYPE = this.checkList[idx].CHECK_TYPE;
                obj.CHECK_DATE = this.targetDate;
                obj.WORK_LINE_CD = this.lineCd;

                this.missPopObj = obj;
            },
            closePop: function() {
                this.missPopObj = null;
            }
        }, created() {
            this.$emit('setMenu', 11); // 좌측메뉴 표기
            this.commonUtil.getPlaceList().then(data => {
                this.placeList = data;
            });
            this.targetDate = this.commonUtil.getToday();
            this.setModeList();
            this.mode = 0;
        }, computed: {
            chgTotal: function() {
                return this.totSize;
            },
            showMsgBox: function() {
                return this.isShow;
            }
        }, watch: {
            currPage: function(newval, oldval) {
                this.getList();
            },
            placeCd: function(newval) {
                this.clearValue('place');
                this.placeCd = newval;
                if (newval == null || newval.length < 1) {
                    return false;
                } 
                this.commonUtil.getPartList().then(data => {
                    this.partList = data;
                });
            },
            partCd: function(newval, oldval) {
                this.clearValue('part');
                this.partCd = newval;
                if (newval == null || newval.length < 1) {
                    return false;
                } 
                this.commonUtil.getComplexList().then(data => {
                    this.complexList = data;
                });
            },
            complexCd: function(newval) {
                this.clearValue('complex');
                this.complexCd = newval;
                if (newval == null || newval.length < 1) {
                    return false;
                } 
                this.commonUtil.getLineList().then(data => {
                    this.lineList = data;
                });
            }
        }
    }
</script>